import {
  Box,
  Checkbox,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
} from "@material-ui/core";
import Icon from "@material-ui/core/Icon";
import { useCallback, useEffect, useState } from "react";
import { Category } from "../../../models";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import Collapse from "@material-ui/core/Collapse";
import { isIndexSignatureDeclaration } from "typescript";

interface Props {
  items: Category[];
  onChange(values: Category[]): any;
  categoriesList: Cat[];
}
interface Cat {
  name?: string | undefined;
  icon?: string | undefined;
  items?: string[];
}
export function CategoryPicker(props: Props) {
  const [Categories, setCategories] = useState<Cat[]>([]);

  useEffect(() => {
    setCategories(props.categoriesList);
  }, []);
  const onChecked = useCallback(
    (category: Category, check: boolean) => {
      let next = [...props.items];
      if (check) {
        if (!next.includes(category)) {
          next.push(category);
        }
      } else {
        next = next.filter((c) => c !== category);
      }
      props.onChange(next);
    },
    [props]
  );

  const onCheckedMain = useCallback(
    (category: string[], check: boolean, mainCat: Cat) => {
      let next = [...props.items];

      if (category.length === 0) {
        if (check) {
          if (!next.includes(mainCat.name as Category)) {
            next.push(mainCat.name as Category);
          }
        } else {
          next = next.filter((c) => c !== (mainCat.name as Category));
        }
      } else {
        category.forEach((s) => {
          if (check) {
            if (!next.includes(s as Category)) {
              next.push(s as Category);
            }
          } else {
            next = next.filter((c) => c !== (s as Category));
          }
        });
      }

      props.onChange(next);
    },
    [props]
  );
  const [open, setOpen] = useState(false);
  const [openIdx, setOpenIdx] = useState([]);

  const handleClick = (i:number) => {
   if(openIdx.includes(i)) {
     const a = openIdx.filter(id=> id !== i);
     setOpenIdx(a);

   } else {
    setOpenIdx([...openIdx,i]);
   }
  };
  const [filter,setfilter]=useState('');
  const searchText=(e:any)=>{
       setfilter(e.target.value);
  }
 console.warn(filter)

let dataSearch=  Categories.filter(data=>{
    return Object.keys(data).some(key=>data[key].toString().toLowerCase().includes(filter.toString().toLowerCase()))
  })
 

  return (
    <Box >
      <input  style={{width:"18vw",margin:"12px",height:'2rem' }}type="text" placeholder="search category"  value={filter}  onChange={searchText.bind(this)} />
      <List dense disablePadding>
        {Categories.length > 0 &&
          dataSearch.map((c,idxs) => {
            let mainchecked = true;
            // @ts-ignore: Object is possibly 'null'.
            if (c.items.length === 0) {
              if (!props.items.includes(c.name as Category)) {
                mainchecked = false;
              }
            } else {
              // @ts-ignore: Object is possibly 'null'.
              c.items.forEach((s) => {
                if (!props.items.includes(s as Category)) {
                  mainchecked = false;
                }
              });
            }

            return (
              <div style={{ display: "flex", flexDirection: "column",width:'100%' }}>
                <ListItem dense button key={c.name} onClick={()=>{handleClick(idxs)}}>
                  <ListItemAvatar>
                    <div style={{ display: "flex" }}>
                      <Checkbox
                        color="primary"
                        size="small"
                        checked={mainchecked}
                        onChange={() =>
                          onCheckedMain(c.items ?? [], !mainchecked, c)
                        }
                      />
                      <Icon
                        color="primary"
                        style={{ margin: "7px", fill: "green" }}
                      >
                        {c.icon}
                      </Icon>
                    </div>
                  </ListItemAvatar>
                  <ListItemText primary={c.name} />
                  {c.name === "Vlogging" ||
                  c.name === "Podcast" ||
                  c.name === "Pranks" ||
                  c.name === "Tech & Gadget"||
                  c.name === "Entertainment"||
                  c.name === "Couples"||
                  c.name === "Parents(Moms&Dad)"||
                  c.name === "Students"||
                  c.name === "Wedding"||
                  c.name === "Toy,Childern&Baby"
                   ? (
                    ""
                  ) : openIdx.includes(idxs) ? (
                    <ExpandLess />
                  ) : (
                    <ExpandMore />
                  )}
                </ListItem>
                <div>
                  <List dense disablePadding>
                    {props.items !== null ? (
                      <>
                        <Collapse
                          in={openIdx.includes(idxs)}
                          timeout="auto"
                          unmountOnExit
                          style={{ marginLeft: "29px" }}
                        >
                          {/* @ts-ignore: Object is possibly 'null'. */}
                          {c.items.map((item) => {
                            let checked = props.items.includes(
                              item as Category
                            );
                            return (
                              <div>
                                <label
                                  key={item}
                                  style={{
                                    cursor: "pointer",
                                    display: "block",
                                  }}
                                >
                                  <ListItem dense button key={item}>
                                    <ListItemAvatar>
                                      <div>
                                        <Checkbox
                                          color="primary"
                                          size="small"
                                          checked={checked}
                                          onChange={() =>
                                            onChecked(
                                              item as Category,
                                              !checked
                                            )
                                          }
                                        />
                                      </div>
                                    </ListItemAvatar>
                                    <ListItemText primary={item} />
                                  </ListItem>
                                </label>
                              </div>
                            );
                          })}
                        </Collapse>
                      </>
                    ) : null}
                  </List>
                </div>
                {/* </ListItem> */}
              </div>
            );
          })}
      </List>
    </Box>
  );
}
