import axios from 'axios';
import { AnyAction } from 'redux';
import { ThunkAction } from 'redux-thunk';
import { IAppState } from '.';
import {
	setBrands,
	setCampaigns,
	setCurrentUser,
	setUserProfile,
} from './action-creators';

export function initializeState(): ThunkAction<
	Promise<any>,
	IAppState,
	{},
	AnyAction
> {
	return async (dispatch, getState) => {
		return new Promise(async (res, rej) => {
			try {
				//TODO: Add all fetches
				await dispatch(fetchLoggedInUser());
				//await dispatch(fetchUserProfile());
				// await dispatch(fetchUserBrands());
				// await dispatch(fetchUserCampaigns());
				res(true);
			} catch (error) {
				console.log(error);
				res(false);
			}
		});
	};
}

export function fetchLoggedInUser(): ThunkAction<
	Promise<any>,
	IAppState,
	{},
	AnyAction
> {
	return async (dispatch, getState) => {
		return new Promise(async (res, rej) => {
			try {
				const token = localStorage.getItem('token');
				if(token) {
					const user = await axios.get('https://devapi.wobb.ai/api/user/info',{headers:{'Authorization':token}}) 
				dispatch(setCurrentUser(user.data.user));
				res(true);
				} else {
					res(false);
				}
				
			} catch (error) {
				res(false);
			}
		});
	};
}

