import { css } from '@emotion/css';
import { Box,Popover,IconButton,Button, Chip,InputAdornment, MenuItem, TextField, Typography } from '@material-ui/core';
import { MouseEvent,useCallback, useState,useEffect } from 'react';
import { CategoryPicker } from '../ui/pickers/CategoryPicker';
import { __tr } from '../../i18n';
import { StyledSlider } from './StyledSlider';
import { MdArrowDropDown } from 'react-icons/md';
import axios from 'axios'
import Location from '../ui/Location';
import { Category } from '../../models/index';
import { grey } from '@material-ui/core/colors';


const locationsList = ["Amharic",
"Arabic",
"Basque",
"Bengali",
"English",
"Portuguese (Brazil)",
"Bulgarian",
"Catalan",
"Cherokee",
"Croatian",
"Czech",
"Danish",
"Dutch",
"Estonian",
"Filipino",
"Finnish",
"French",
"German",
"Greek",
"Gujarati",
"Hebrew	",
"Hindi",
"Hungarian",
"Icelandic",
"Indonesian",
"Italian",
"Japanese",
"Kannada",
"Korean",
"Latvian",
"Lithuanian",
"Malay",
"Malayalam",
"Marathi",
"Norwegian",
"Polish",
"Portuguese",
"Romanian",
"Russian",
"Serbian",
"Chinese",
"Slovak",
"Slovenian",
"Spanish",
"Swahili",
"Swedish",
"Tamil",
"Telugu",
"Thai",
"Chinese",
"Turkish",
"Urdu",
"Ukrainian",
"Vietnamese",
"Welsh"]


const styles = {
  root: css`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  `,
};

interface InfluencerFilter {
  sortBy?: string;
  followerRange: {
    min: number;
    max?: number;
  };
  engagementRate: number;
  category?: string[];
  gender?: string;
  age: number;
  location?: string[];
  language?: string[];
}

export const initialFilter: InfluencerFilter = {
  followerRange: {},
  age: 21,
  engagementRate: 1.5,
  language: [],
  location: []
};

interface FilterUIProps {
  enableSorting?: boolean;
  onApply(value: InfluencerFilter): any;
}

interface Cat {
	name?: string | undefined;
	icon?: string | undefined;
	items?: string[];
}

export function InfluencerFilterUi(props: FilterUIProps) {
  const [filter, setFilter] = useState<InfluencerFilter>(initialFilter);
  const [CategoriesList, setCategoriesList] = useState<Cat[]>([]);
  const [categoryPickerAnchor, setCategoryPickerAnchor] =
		useState<HTMLDivElement>();

    const onOpenCategory = useCallback((ev: MouseEvent<HTMLDivElement>) => {
      setCategoryPickerAnchor(ev.currentTarget);
    }, []);
  
    const onCloseCategory = useCallback(() => {
      setCategoryPickerAnchor(undefined);
    }, []);

    useEffect(() => {	
      const token = localStorage.getItem('token');
        axios.get('/api/campaign/categories',{headers: {'Authorization':token}}).then(res => {
          setCategoriesList(res.data);
      });
    }, [])

  const onRemoveLanguage = (add:string) => {
    let abs = {
      ...filter,
    };
    const idx = (abs.language?? []).indexOf(add);
    const lang:string[] = [];
    (abs.language?? []).forEach((a,i)=>{
      if(i!==idx) {
        lang.push(a);
      }
    })
    abs.language = lang;
    setFilter(abs);

  }

  const onRemoveCategory = (add:string) => {
    let abs = {
      ...filter,
    };
    const idx = (abs.category?? []).indexOf(add);
    const lang:string[] = [];
    (abs.category?? []).forEach((a,i)=>{
      if(i!==idx) {
        lang.push(a);
      }
    })
    abs.category = lang;
    setFilter(abs);

  }

  const onMinFollowerChange = useCallback(
    (val: number | string) => {
      let abs = {
        ...filter,
      };
      abs.followerRange.min = parseInt(`${val}`) || 1000;
      setFilter(abs);
    },
    [filter]
  );

  const onMaxFollowerChange = useCallback(
    (val: number | string) => {
      let abs = {
        ...filter,
      };
      abs.followerRange.max = parseInt(`${val}`) || 1000000;
      setFilter(abs);
    },
    [filter]
  );

  const onEngagementRateChange = useCallback(
    (val: number) => {
      let abs = {
        ...filter,
      };
      abs.engagementRate = val;
      setFilter(abs);
    },
    [filter]
  );

  const onSortParameterChange = useCallback(
    (val: string) => {
      let abs = {
        ...filter,
      };
      abs.sortBy = val;
      setFilter(abs);
    },
    [filter]
  );

  const onCategoryChange = useCallback(
    (val: string[]) => {
      let abs = {
        ...filter,
      };
      abs.category = val;
      setFilter(abs);
    },
    [filter]
  );

  const onGenderChange = useCallback(
    
    (val: string) => {
      let abs = {
        ...filter,
      };
      abs.gender = val;
      setFilter(abs);
    },
    [filter]
  );

  const onAgeChange = useCallback(
    (val: number) => {
      let abs = {
        ...filter,
      };
      abs.age = val;
      setFilter(abs);
    },
    [filter]
  );

  const onLocationChange = useCallback(
    (val: string[]) => {
      let abs = {
        ...filter,
      };
      abs.location = val;
      setFilter(abs);
    },
    [filter]
  );

  const onLanguageChange = useCallback(
    (val: string) => {
      let abs = {
        ...filter,
      };
      abs.language = [...abs.language ?? [], val];
      setFilter(abs);
    },
    [filter]
  );

  return (
    <Box paddingX={2} className={styles.root}>
      {props.enableSorting && (
        <Box marginY={2} width='100%'>
          <Box >
            <Typography variant='body2'>{__tr('sortBy')}</Typography>
          </Box>
          <TextField
            size='small'
            select
            value={filter.sortBy}
            onChange={(ev) => onSortParameterChange(ev.currentTarget.value)}
            variant='outlined'
            placeholder='e.g Engagement Rate'
            fullWidth
          >
            <MenuItem value='engagementRate'>{__tr('engagementRate')}</MenuItem>
          </TextField>
        </Box>
      )}
      <Box marginY={1} width='100%'>
        <Box >
          <Typography variant='body2'>{__tr('followersRange')}</Typography>
        </Box>
        {/* <StyledSlider
          min={1}
          max={1000}
          track='inverted'
          valueLabelDisplay='auto'
          marks={[
            { value: 1, label: '1K' },
            { value: 1000, label: '1M+' },
          ]}
          value={filter.followerRange.min}
          onChange={(ev, val) => onMinFollowerChange(val as number)}
        /> */}
      </Box>
      <Box
        width='100%'
        marginY={1}
        display='flex'
        flexDirection='row'
        alignItems='center'
        justifyContent='center'
      >
        <Box marginRight={0.5}>
          <TextField
            fullWidth
            variant='outlined'
            size='small'
            placeholder='e.g 10'
            type='number'
            value={filter.followerRange.min}
            onChange={(ev) => onMinFollowerChange(ev.currentTarget.value)}
            InputProps={{
              inputProps: {
                style: { textAlign: "right" },
              },
              startAdornment: <InputAdornment position='start'>{__tr('min')}</InputAdornment>,
            }}
          />
        </Box>
        <Box marginLeft={0.5}>
          <TextField
            fullWidth
            variant='outlined'
            size='small'
            type='number'
            placeholder='e.g 1000'
            value={filter.followerRange.max}
            onChange={(ev) => onMaxFollowerChange(ev.currentTarget.value)}
            InputProps={{
              inputProps: {
                style: { textAlign: "right" },
              },
              startAdornment: <InputAdornment position='start'>{__tr('max')}</InputAdornment>
            }}
          />
        </Box>
      </Box>
      <Box width='100%' marginY={1}>
        <Box>
          <Typography variant='body2'>{__tr('engagementRate')}</Typography>
        </Box>
        <StyledSlider
          min={1}
          max={5}
          track='inverted'
          valueLabelDisplay='auto'
          marks={[
            { value: 1, label: '1%' },
            { value: 5, label: '5%+' },
          ]}
          value={filter.engagementRate}
          onChange={(ev, val) => onEngagementRateChange(val as number)}
        />
      </Box>
      <Box width='100%' marginY={1}>
        <Box>
          <Typography variant='body2'>{__tr('category')}</Typography>
        </Box>
        
        <Box
								onClick={onOpenCategory}
								style={{
									borderRadius: '4px',
									border: `1px solid ${grey[400]}`,
									width: '100%',
									display: 'flex',
									flexDirection: 'row',
									justifyContent: 'flex-start',
								}}
							>
								<Box
									flexGrow={1}
									style={{
										height: '40px',
										overflowX: 'auto',
										scrollbarWidth: 'thin',
										display: 'flex',
										flexDirection: 'row',
										justifyContent: 'flex-start',
									}}
								>
									{filter.category && filter.category.length === 0 ? (
										<p
											style={{
												color: '#848484',
												paddingLeft: '0.7rem',
												marginTop: '0.6rem',
												fontSize: '1rem',
												fontWeight: '300',
											}}
										>
											e.g. Food
										</p>
									) : (
										(filter.category ?? []).map(c => {
											return (
												<Chip
													size='small'
													variant='outlined'
													color='primary'
													label={c}
													clickable
													onDelete={() => onRemoveCategory(c)}
													style={{ marginLeft: '4px', marginTop: '4px' }}
												/>
											);
										})
									)}
								</Box>

								<IconButton size='small'>
									<MdArrowDropDown size={24} />
								</IconButton>
							</Box>
							<Popover
								elevation={2}
								open={Boolean(categoryPickerAnchor)}
								onClose={onCloseCategory}
								anchorEl={categoryPickerAnchor}
								anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
								transformOrigin={{ vertical: 'top', horizontal: 'left' }}
							>
								<Box maxHeight='320px'>
									<CategoryPicker items={(filter.category ?? []) as Category[]} onChange={onCategoryChange} categoriesList={CategoriesList} />
								</Box>
							</Popover>
      </Box>
      <Box width='100%' marginY={1}>
        <Box >
          <Typography variant='body2'>{__tr('gender')}</Typography>
        </Box>
        <TextField
          select
          value={filter.gender}
          onChange={(ev) => onGenderChange(ev.target.value)}
          size='small'
          variant='outlined'
          placeholder='e.g Male'
          fullWidth
        >
          <MenuItem value='MALE'>Male</MenuItem>
          <MenuItem value='FEMALE'>Female</MenuItem>
        </TextField>
      </Box>
      <Box width='100%' marginY={1}>
        <Box>
          <Typography variant='body2'>{__tr('age')}</Typography>
        </Box>
        <StyledSlider
          min={19}
          max={45}
          value={filter.age}
          track='inverted'
          valueLabelDisplay='auto'
          marks={[
            { value: 19, label: '19' },
            { value: 45, label: '45+' },
          ]}
          onChange={(ev, val) => onAgeChange(val as number)}
        />
      </Box>
      <Box width='100%' marginY={1}>
        <Box>
          <Typography variant='body2'>{__tr('location')}</Typography>
        </Box>
        <Location onChange={(val:string[])=>onLocationChange(val)}/>
        {/* <TextField
          size='small'
          select
          value={filter.location}
          onChange={(ev) => onLocationChange(ev.currentTarget.value)}
          variant='outlined'
          fullWidth
        >
          <MenuItem value='Mumbai'>Mumbai</MenuItem>
          <MenuItem value='New Delhi'>New Delhi</MenuItem>
        </TextField> */}
      </Box>
      <Box width='100%' marginY={1}>
        <Box>
          <Typography variant='body2'>{__tr('language')}</Typography>
        </Box>
        <TextField
          size='small'
          select
          //value={filter.language}
          value={''}
          onChange={(ev) => {onLanguageChange(ev.target.value)}}
          variant='outlined'
          fullWidth
        >
           {locationsList.map(l=>{
            return (<MenuItem value={l}>{l}</MenuItem>)
          })}
        </TextField>
        {filter?.language && filter.language.map(c => {
											return (
												<Chip
													size='small'
													variant='outlined'
													color='primary'
													label={c}
													clickable
													onDelete={() => onRemoveLanguage(c)}
													style={{ marginLeft: '4px', marginTop: '-60px' }}
												/>
											);
										})}
      </Box>

      <Button
					variant='outlined'
					color='default'
					size='small'
					onClick={()=>{props.onApply(filter)}}
					//disabled={busy}
					style={{ width: '124px', padding: '10px', fontSize: '13px' }}
				>
					Apply
				</Button>
    </Box>
  );
}
