import { TopAppBar } from "./TopAppBar";
import styles from "../styles/Home.module.scss";
import { Box } from "@material-ui/core";
import { MyCampaigns } from "./campaigns/MyCampaigns";
import { Redirect, Route, Switch } from "react-router";
import { Routes } from "../routes";
import { ViewCampaign } from "./campaigns/ViewCampaign";
// import { DiscoverScreen } from './discover';
import { CreateCampaign } from "./campaigns/create/CreateCampaign";
// // import { ProfileScreen } from './profile/ProfileScreen';
// import { NotificationScreen } from './notifications/NotificationScreen';
import { FixedMargin } from "./ui/FixedMargin";
// import { UserManagementView } from './user-management/UserManagementView';
import { ThreadsView } from "./messaging/ThreadsView";
import { useWindowSize } from "../core/hooks";
//import ProductPage from './StoreSection/ProductDescription/ProductPage';
//import HomePage from './StoreSection/ProductPage/HomePage';
import axios from "axios";
import { useDispatch } from "react-redux";
import { useState, useEffect } from "react";
import { setCurrentUser } from "../state/action-creators";
import { SuperAdmin } from "../components/superadmin/SuperAdmin";
import {EditCampaign} from "../components/campaigns/create/EditCampaign";

axios.defaults.baseURL =
  "http://aa62100d41cee4c06a48c5dca26c6679-1265921153.ap-south-1.elb.amazonaws.com";

export function Home() {
  const [width] = useWindowSize();
  const dispatch = useDispatch();

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      axios
        .get("/api/user/info", { headers: { Authorization: token } })
        .then((res) => {
          dispatch(setCurrentUser(res.data.user));
        });
    }
  }, []);

  return (
    <div className={styles.page}>
      {width >= 900 && (
        <div className={styles.header}>
          <TopAppBar />
        </div>
      )}
      <Box className={styles.main}>
        <Switch>
          <Route path={Routes.viewCampaign(":id")}>
            <FixedMargin>
              <ViewCampaign />
            </FixedMargin>
          </Route>
          <Route path={Routes.BaseCampaigns} exact>
            <FixedMargin>
              <MyCampaigns />
            </FixedMargin>
          </Route>
          <Route path={Routes.EditCampaign(":id")}>
            <FixedMargin>
              <EditCampaign/>
            </FixedMargin>
          </Route>
          <Route path={Routes.PostSimilarCampaign(":id")}>
            <FixedMargin>
              <CreateCampaign/>
            </FixedMargin>
          </Route>
          <Route path={Routes.Discover}>
            <FixedMargin>{/* <DiscoverScreen /> */}</FixedMargin>
          </Route>
          <Route path={Routes.MyProfile}>
            <FixedMargin>{/* <ProfileScreen /> */}</FixedMargin>
          </Route>
          <Route path={Routes.CreateCampaign}>
            <FixedMargin>
              <CreateCampaign />
            </FixedMargin>
          </Route>
          <Route path={Routes.Notifications}>
            <FixedMargin>{/* <NotificationScreen /> */}</FixedMargin>
          </Route>
          <Route path={Routes.UserManagement}>
            <FixedMargin>{/* <UserManagementView /> */}</FixedMargin>
          </Route>
          <Route path={Routes.Messages}>
            <ThreadsView />
          </Route>
          <Route path={Routes.Home} exact>
            {/* <Redirect to={Routes.campaigns('applied')} /> */}
            <Redirect to={Routes.BaseCampaigns} />
          </Route>
          <Route path={Routes.SuperAdmin}>
            <FixedMargin>
              <SuperAdmin />
            </FixedMargin>
          </Route>
        </Switch>
      </Box>
    </div>
  );
}
