import {
  Box,
  Container,
  Grid,
  Paper,
  IconButton,
  Popover,
  Hidden,
} from "@material-ui/core";
import { useLocation } from "react-router";
import { useWindowSize } from "../../core/hooks";
import { AppMetadata } from "../ui/AppMetadata";
import { BoxNewCampaign } from "./BoxNewCampaign";
import { CampaignStatusList } from "./CampaignStatusList.tsx";
import { PostedCampaignTable } from "./PostedCampaignTable";
import { CampaignTabs } from "./CampaignTabs";
import { useEffect, useState, useMemo } from "react";
import { CampaignFilters } from "../../core";
import qs from "querystring";
import MobileHeader from "../ui/mobileHeader";
import { RiSearchLine } from "react-icons/ri";
import styles from "../messaging/mobile/messagesList/messagesList.module.scss";
import { useDispatch } from "react-redux";
import { setCampaigns } from "../../state/action-creators";
import axios from "axios";
import { Campaign } from "../../models/index";
import { useAppUser } from "../../state/selectors";

export function MyCampaigns() {
  const { user } = useAppUser();
  const dispatch = useDispatch();
  const [filter, setActiveFilter] = useState(CampaignFilters[0]);
  const [statusFlag, setstatusFlag] = useState<any>();
  const location = useLocation();
  const status = useMemo(
    () => qs.parse(location.search.replace("?", "")).filter || "posted",
    [location]
  );

  const [width] = useWindowSize();

  // const MessagesList: React.FC<Props> = ({ onCreateGroupClick }) => {
  // state
  const [openOptions, setOpenOptions] = useState<boolean>(false);
  const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement>();

  //open options modal
  const openOptionsModal = (el: HTMLElement) => {
    setMenuAnchorEl(el);
    setOpenOptions(true);
  };

  useEffect(() => {
    if (user) {
      const token = localStorage.getItem("token");
      axios
        .get("/api/campaign/" + user._id, { headers: { Authorization: token } })
        .then((res) => {
          const list: Campaign[] = res.data ?? [];
          dispatch(setCampaigns(list));
        });
    }
  }, [user]);

  return (
    <Grid container spacing={3} style={{ marginTop: "60px" }}>
      <Hidden mdUp>
        <Grid item xs={12}>
          <Box className={styles.mobileHeaderContainer}>
            <MobileHeader pageTitle="My Campaigns">
              <div className={styles.HeaderChildren}>
                <IconButton key="1">
                  <RiSearchLine size={25} />
                </IconButton>
                {/* <IconButton
              key='2'
              onClick={(ev: React.MouseEvent<HTMLElement>) => {
                openOptionsModal(ev.currentTarget);
              }}
            >
              <MoreVert fontSize='medium' />
            </IconButton> */}
              </div>
            </MobileHeader>
          </Box>
        </Grid>
      </Hidden>

      {/* <Grid item xs={12}>
				<Box paddingY={2}>
					<Container>
						<CampaignTabs />
					</Container>
				</Box>
			</Grid>  */}
      <Grid item xs={12} md={4} lg={3}>
        <Paper elevation={0} style={{position:"sticky",top:"70px"}}>
          <CampaignStatusList
            setstatusFlag={setstatusFlag}
            filter={filter}
            onChange={setActiveFilter}
          />
        </Paper>
      </Grid>

      <Grid item xs={12} md={8} lg={6}>
        <Paper elevation={0}>
          {status && status === "posted" && (
            <PostedCampaignTable statusFlag={statusFlag} />
          )}
        </Paper>
      </Grid>

      <Grid item md={12} lg={3}>
        <Grid container spacing={2}>
          <Grid item xs={12} md={6} lg={12}>
            <Paper elevation={0}>
              <BoxNewCampaign />
            </Paper>
          </Grid>
          {/* <Grid item xs={12} md={6} lg={12}>
            <Paper elevation={0}>
              <BoxRecommendedCampaigns />
            </Paper>
          </Grid> */}
          <Grid item xs={12}>
            <Box>
              <AppMetadata />
            </Box>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}
