import { Brand, Campaign, User,Application } from '../models/index';
import { IAction } from './actions';

export function setCurrentUser(user: any): IAction {
	return {
		type: 'set_active_user',
		user,
	};
}

export function setApplicants(applicants: Application[]): IAction {
	return {
		type: 'set_applicants',
		applicants,
	};
}

export function setApplicantsApprove(applicantsapprove: Application[],actionType: string): IAction {
	return {
		type: 'set_applicants_approve',
		applicantsapprove,actionType
	};
}

export function setSelectedCampaignId(selectedcampaignid: string): IAction {
	return {
		type: 'set_selected_campaignid',
		selectedcampaignid,
	};
}

export function setSelectedCampaign(selectedcampaign: Campaign): IAction {
	return {
		type: 'set_selected_campaign',
		selectedcampaign,
	};
}

export function setUserProfile(profile: User): IAction {
	return {
		type: 'set_user_profile',
		profile,
	};
}

export function signUserOut(): IAction {
	return {
		type: 'unset_active_user',
	};
}

export function setBrands(brands: Brand[]): IAction {
	return {
		type: 'set_brands',
		brands,
	};
}

export function setCampaigns(campaigns: Campaign[]): IAction {
	return {
		type: 'set_campaigns',
		campaigns,
	};
}

export function appendBrand(brand: Brand): IAction {
	return {
		type: 'append_brand',
		brand,
	};
}

export function appendCampaign(campaign: Campaign): IAction {
	return {
		type: 'append_campaign',
		campaign,
	};
}

export function removeBrand(brandId: string): IAction {
	return {
		type: 'remove_brand',
		brandId,
	};
}

export function removeCampaign(campaignId: string): IAction {
	return {
		type: 'remove_campaign',
		campaignId,
	};
}
