import { GlobalState } from ".."
import { IAction } from "../actions"
import { Campaign } from '../../models/index';

const initialState: GlobalState = {
    campaigns: [],
    brands: [],
    applicants: [],
    applicantsapprove: [],
    selectedcampaign: null,
    selectedcampaignid: '',
    
}

export const globalReducer = (state: GlobalState = initialState, action: IAction) => {
    let next = {...state};
    if(action.type === 'set_campaigns') {
        next.campaigns = action.campaigns;
    }
    else if(action.type === 'set_applicants_approve') {
         if(action.actionType === 'add') {
            next.applicantsapprove = [...next.applicantsapprove,...action.applicantsapprove];
        } else {
            const idx = next.applicantsapprove.findIndex(a=> a._id ===  action.applicantsapprove[0]._id && a.selectedIdx === action.applicantsapprove[0].selectedIdx);
            if(idx >= 0) {
                next.applicantsapprove.splice(idx,1);
            }
      
            next.applicantsapprove = [...next.applicantsapprove];
        }
        
    }
    else if(action.type === 'set_applicants') {
        next.applicants = action.applicants;
    }
    else if(action.type === 'set_selected_campaign') {
        next.selectedcampaign = action.selectedcampaign;
    }
    else if(action.type === 'set_selected_campaignid') {
        next.selectedcampaignid = action.selectedcampaignid;
    }
    else if(action.type === 'set_brands') {
        next.brands = action.brands;
    }
    else if(action.type === 'append_brand') {
        let brandIndex = next.brands.findIndex((b) => {
            return b.brandId === action.brand.brandId;
        })
        if(brandIndex === -1) {
            next.brands = [action.brand, ...next.brands];
        }
        else {
            next.brands[brandIndex] = action.brand
        }
    }
    else if(action.type === 'append_campaign') {
        let campaignIndex = next.campaigns.findIndex((c) => {
            return c.id === action.campaign.id;
        })
        if(campaignIndex === -1) {
            next.campaigns = [action.campaign, ...next.campaigns];
        }
        else {
            next.campaigns[campaignIndex] = action.campaign
        }
    }
    else if(action.type === 'remove_brand') {
        next.brands = next.brands.filter((b) => {
            return b.brandId !== action.brandId;
        })
    }
    else if(action.type === 'remove_campaign') {
        next.campaigns = next.campaigns.filter((c) => c.id !== action.campaignId)
    }
    return next;
}