import { Box } from '@material-ui/core';
import {
	ChangeEvent,
	useCallback,
	useContext,
	useEffect,
	useState,
} from 'react';
import { Link, useHistory } from 'react-router-dom';
import { RoleInputField } from '../ui/RoleInputField';
import { SingleLineInput } from '../ui/SingleLineInput';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/Signup.module.scss';
import { toast } from 'react-toastify';
import { Loader } from '../ui/Loader';
import { useUrlQuery } from '../../core/hooks';
import PasswordStrengthBar from 'react-password-strength-bar';

import axios from 'axios';

export function OauthSignup() {
	const userId = useUrlQuery('userid', '');
	const [emailOrPhone, setEmailOrPhone] = useState('');
	const [selectedRole, setSelectedRole] = useState<string>('INFLUENCER');
	const [password, setPassword] = useState('');
	const [errors, setErrors] = useState<any>({});
	const [loading, setLoading] = useState(false);
	//const { user, setUser } = useContext(UserContext);
	const  user = JSON.parse(localStorage.getItem('user') || '{}');
	const currentPassword = localStorage.getItem('pwd');

	useEffect(() => {}, []);

	const history = useHistory();

	const onFormChange = useCallback((ev: ChangeEvent<HTMLFormElement>) => {
		setErrors({});
		setLoading(false);
	}, []);

	const onSubmit = useCallback(
		(ev: ChangeEvent<HTMLFormElement>) => {
			ev.preventDefault();

			if (loading) {
				/// does nothing if there is an ongoing action
				return;
			}

			let form = ev.currentTarget;
			let newErrors: any = {};

			let data = {
				password: form.password.value,
				role: selectedRole,
				userId
			};

			// if (!Validators.isValidPassword(data.password)) {
			// 	newErrors.password = __tr('errorInvalidPassword');
			// }
			let passwordStatus=Validators.isValidPasswordNew(data.password)
			if (!passwordStatus.valid) {
			  newErrors.password = __tr(passwordStatus.message);
			}

			if (Object.keys(newErrors).length === 0) {
				setLoading(true);
				const token = localStorage.getItem('token');
				
				axios.post('/api/user/oauth/password',data,{headers: {'Authorization': token}})
				.then(res=>{
					setLoading(false);
					history.push(Routes.Home);
				})
				
			}
			setErrors(newErrors);
		},
		[selectedRole, loading, history,userId]
	);
	return (
		<div className={styles.loginWrapper}>
			<h3>{__tr('welcomeToWobb')}</h3>
			<div className={styles.formWrapper}>
				<h3>{__tr('signup')}</h3>
				{loading && <Loader />}
				<form
					onChange={onFormChange}
					action=''
					className={styles.form}
					onSubmit={onSubmit}
				>
					<div className={styles.socialAccount}>
						<div className={styles.header}>
							<span className={styles.name}>{user?.name}</span>
						</div>
						<div className={styles.content}>
							<span className={styles.email}>{user?.email}</span>
						</div>
					</div>

					<Box marginY={2}>
						<SingleLineInput
							label={__tr('labelCreatePassword')}
							type='password'
							name='password'
							placeholder={__tr('placeholderSignupPassword')}
							inputProps={{
								onChange: ev => setPassword(ev.currentTarget.value),
								value: password,
							}}
							error={errors.password}
						/>
						{password && <PasswordStrengthBar password={password} />}
					</Box>
					<Box marginY={2}>
						<RoleInputField
							label={__tr('labelSelectRole')}
							value={selectedRole}
							onChange={setSelectedRole}
						/>
					</Box>
					<Box marginY={2}>
						<p className={styles.textAcceptTerms}>
							{__tr('byRegistering')}, {__tr('IAgreeTo')}
							<Link to={Routes.TermsAndCondition} className={styles.link}>
								{__tr('termsAndConditions')}
							</Link>
							{__tr('and')}
							<Link to={Routes.PrivacyPolicies} className={styles.link}>
								{__tr('privacyPolicies')}
							</Link>
						</p>
					</Box>
					{!loading && (
						<Box marginY={2}>
							<button type='submit' className={styles.btnSubmit}>
								<span className={styles.text}>{__tr('btnAgreeAndJoin')}</span>
							</button>
						</Box>
					)}
				</form>
			</div>
		</div>
	);
}
