import { useState, useEffect } from "react";
import GooglePlacesAutocomplete, {
  geocodeByPlaceId
} from "react-google-places-autocomplete";
import { useParams } from 'react-router';
import axios from 'axios';

import {
	Chip
} from '@material-ui/core';


export default function Location(props) {
  const token = localStorage.getItem('token');
  const [address, setAddress] = useState([]);
  //const [address, setAddress] = useState();
  const [addressObj, setAddressObj] = useState();
  const {id} =useParams();
    const [data,accessData]=useState({
        Categories:[],
		Platform:[],
    })
    useEffect(()=>{
       if(id!== undefined && token){
      axios.get(`/api/campaign/id/${id}`,{headers: { Authorization: token }}).then(result => {
       // if(result.data[0].address !== null){
                   setAddress(result.data[0].location?? [])
       // }
})
                  }
             
    },[token])

  const getAddressObject = (address_components) => {
    console.log(address_components);
    const ShouldBeComponent = {
      street_number: ["street_number"],
      postal_code: ["postal_code"],
      street: ["street_address", "route"],
      province: [
        "administrative_area_level_1",
        "administrative_area_level_2",
        "administrative_area_level_3",
        "administrative_area_level_4",
        "administrative_area_level_5"
      ],
      city: [
        "locality",
        "sublocality",
        "sublocality_level_1",
        "sublocality_level_2",
        "sublocality_level_3",
        "sublocality_level_4"
      ],
      country: ["country"]
    };

    let address = {
      street_number: "",
      postal_code: "",
      street: "",
      province: "",
      city: "",
      country: ""
    };

    address_components.forEach((component) => {
      for (var shouldBe in ShouldBeComponent) {
        if (ShouldBeComponent[shouldBe].indexOf(component.types[0]) !== -1) {
          if (shouldBe === "country") {
            address[shouldBe] = component.short_name;
          } else {
            address[shouldBe] = component.long_name;
          }
        }
      }
    });

    // Fix the shape to match our schema
    address.address = address.street_number + " " + address.street;
    delete address.street_number;
    delete address.street;
    if (address.country === "US") {
      address.state = address.province;
      delete address.province;
    }
    return address;
  };

  // useEffect(() => {
  //   const func = async () => {
  //     const geocodeObj =
  //       address &&
  //       address.value &&
  //       (await geocodeByPlaceId(address.value.place_id));
  //     const addressObject =
  //       geocodeObj && getAddressObject(geocodeObj[0].address_components);
  //     console.log("addressObject", addressObject);
  //     setAddressObj(addressObject);
  //   };
  //   func();
  // }, [address]);

  const onRemoveAddress = (add:string) => {
    const idx = address.indexOf(add);
    const addr = [];
    address.forEach((a,i)=>{
      if(i!==idx) {
        addr.push(a);
      }
    })
    setAddress(addr);
    props.onChange(addr)
  }
  // useEffect(()=>{
	// 	const splitted:[] = props.value?.split(' ');
	// 	setAddress(splitted?.length >1 ? splitted[1] : props.value)
	// 	console.log('props.value',props.value);
	// },[props.value])
  return (
    <>
      <GooglePlacesAutocomplete
        apiKey="AIzaSyA-w9SjbX_poCmpX0mKI3qfSwK-gdHNU2c"
        selectProps={{
          isClearable: true,
         // value:{address},
          placeholder: 'Enter Location',

          onChange: (val) => {
            //props.onChange(val?.label ?  val.label : '')
           props.onChange(val?.label ? [ val.label] : "")
            if(val?.label) {
              const newAdd = address;
              newAdd.push(val.label);
              setAddress(newAdd)
            };
          }
        }}
      />

                  {address.map(c => {
											return (
												<Chip
													size='small'
													variant='outlined'
													color='primary'
													label={c}
													clickable
													onDelete={() => onRemoveAddress(c)}
													style={{ marginLeft: '4px', marginTop: '4px', width:'inherit' }}
												/>
											);
										})}
    </>
  );
}
