	const addQuestions = (value: string, i: number) => {
	  const ques = customQuestions;
	  const res: typeof ques = [];
	  ques.forEach((q, k) => {
		if (i === k) {
		  const n: QuizElement = {
			question: value,
			options: q.options,
		  };
		  res.push(n);
		} else {
		  res.push(q);
		}
	  });
	  setCustomQuestions(res);
	};
  
	const changeOptions = (value: string, i: number, k: number) => {
	  const ques = customQuestions;
	  const res: typeof ques = [];
	  ques.forEach((q, l) => {
		if (i === l) {
		  //@ts-ignore: Object is possibly 'null'.
		  const op: typeof q.options = [];
		  q.options?.forEach((o, j) => {
			if (j === k) {
			  op.push(value);
			} else {
			  op?.push(o);
			}
		  });
		  const n: QuizElement = {
			question: q.question,
			options: op,
		  };
		  res.push(n);
		} else {
		  res.push(q);
		}
	  });
  
	  setCustomQuestions(res);
	};
	const addOptions = useCallback(
	  (i: number) => {
		const temp: typeof customQuestions = [];
		customQuestions.forEach((c, k) => {
		  if (k === i) {
			const nop: (string | null)[] = [...(c.options ?? []), ""];
  
			const n = {
			  question: c.question,
			  options: nop,
			};
			temp.push(n);
		  } else {
			temp.push(c);
		  }
		});
		setCustomQuestions(temp);
	  },
	  [customQuestions]
	);
  
	const removeOptions = useCallback(
	  (i: number, j: number) => {
		const temp: typeof customQuestions = [];
		customQuestions.forEach((c, k) => {
		  if (k === i) {
			const nop: (string | null)[] = c.options ?? [];
			nop.splice(j, 1);
			const n = {
			  question: c.question,
			  options: nop,
			};
			temp.push(n);
		  } else {
			temp.push(c);
		  }
		});
		setCustomQuestions(temp);
	  },
	  [customQuestions]
	);
  
