import { useEffect, useState } from "react";
import {Button, IconButton,List, Menu, MenuItem, Popover } from "@material-ui/core";
import { AttachFile } from "@material-ui/icons";
import { BsEmojiSmile } from "react-icons/bs";
import styles from "./chatControls.module.scss";
import { MessageCard } from "../../MessageCard";
import { Box } from "@mui/system";
import { group } from "console";
import zIndex from "@material-ui/core/styles/zIndex";
import Picker from "emoji-picker-react";
import axios from 'axios';

interface Props {
  groupChat?: boolean;
  socket?: any;
  selectedchat?: any;
}

const ChatControls: React.FC<Props> = ({ groupChat, socket,selectedchat }) => {
  const token = localStorage.getItem("token");
  const [message, setMessage] = useState<string>("");
  const [chatuser,setchatuser] = useState<any>();
  const [emojiPicker, setEmojiPicker] = useState(false);
  const [chosenEmoji, setChosenEmoji] = useState(null);
  const [openSendOptions, setOpenSendOptions] = useState<boolean>(false);
  const [selectuser, setselectuser] = useState<boolean>(false);
  const [taggedUsers, settaggedUsers] = useState([]);
  const [tagedPart,setTagedPart] = useState([]);
  const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement | undefined>(
    undefined
  );

  useEffect(()=>{
    setchatuser(null);
  },[])
  const onEmojiClick = (event, emojiObject) => {
    setChosenEmoji(emojiObject);
    // setEmojiPicker(false);
    // console.log(emojiObject);
    setMessage(message + emojiObject.emoji);
  };

  //open options modal
  const openOptionsModal = (el: HTMLElement) => {
    setMenuAnchorEl(el);
    setOpenSendOptions(true);
  };

  const selectpart = (e:any) => {
    console.log('selectpart', e)
    setselectuser(false);
    setchatuser(e);
    settaggedUsers([...taggedUsers,e]);
    setTagedPart([...tagedPart,e.userId]);
    tagCheck(`${message}`,`${e.userName}`);
  }

  const writemessage = (e:String) => {
    if(taggedUsers.length > 0) {
      const tags =[];
    taggedUsers.forEach(t=>{
      if(e.includes(t.userName)){
        tags.push(t);
      }else if(e.includes(t.userName.substr(0, t.userName.length-1))){
        e = e.replace("@"+t.userName.substr(0, t.userName.length-1),"");
      }
        
    })
    
    settaggedUsers(tags);
    }
    //setMessage(e);
    if(e.includes("@") && selectedchat.isGroupChat) {
      setMessage(e);
      tagCheck(e);
    } else {
      if(chatuser && e.includes(chatuser.userName)) {
        setMessage(e);
      } else {
        setMessage(e);
        setchatuser(null);
      }
    }
    
  }

  useEffect(() => {
    if(selectedchat.chatRoom) {
      console.log('selectedchat.chatRoom', selectedchat.chatRoom);
      setMessage('');
    }
  }, [selectedchat?.chatRoom]);

  const toggleEmojis = () => {
    setEmojiPicker(!emojiPicker);
  }

  useEffect(() => {
    if(!message) {
      setselectuser(false);
    }
  }, [message]);

  const OpenFileExplorer = () => {
    console.log('OpenFileExplorer');
    if(document.getElementById('input_file_attachment')) {
      document.getElementById('input_file_attachment').click();
    }
  }

  function tagCheck(text, taggedPerson) {
    const n = text.split(" ");
    const lastWord = n[n.length - 1];
    let newText = '';
    if(lastWord.includes("@")) {
      if(taggedPerson) {
        // n[sn.length - 1] = "@" + taggedPerson.replace(' ','');
        newText = text.replace(new RegExp(lastWord + '$'), "@" + taggedPerson);
        console.log('newText', newText);
        setMessage(`${newText} `);
      } else {
        setselectuser(true);
        // setMessage(text);
      }
    }
    
    // return newText;
}

  const uploadAttachment = (event) => {
    console.log('uploadAttachment', event?.target?.files?.[0]);
    let reader = new FileReader();
    reader.readAsDataURL(event?.target?.files?.[0]);
    reader.onload = function () {
     
      let mimeType = reader.result.match(/[^:]\w+\/[\w-+\d.]+(?=;|,)/)[0];
      console.log(mimeType,'mimeType');
      const postBody = {
        name:chatuser?.userName,
        file:reader.result,
        contentType:mimeType
      }
      axios.post('/api/s3/upload',postBody,{
        headers: { Authorization: token },
      }).then(res=>{
        const attachment = res.data;
        console.log('attachment', attachment, chatuser, 'message', message);
        // socket.emit("image", { image: true, buffer: attachment.toString('base64') });
        socket.emit("chatMessage", chatuser ? `${attachment}#~#${chatuser.userId}#~#` :  `${attachment}#~#file#~#${event?.target?.files?.[0]?.type}`, event?.target?.files?.[0]?.type);
    });
  };
  }

  return (
    <>
      <div className={styles.chatControlsMobile}>
        <div className={styles.inputMessage}>
          <div className={styles.emojiIcon}>
            <IconButton size="small">
              <BsEmojiSmile />
            </IconButton>
          </div>
          <div>
            <textarea
              value={message}
              placeholder="write a message"
              rows={1}
              onChange={(e) => setMessage(e.target.value)}
            />
          </div>

          <div className={styles.attachFile}>
            <Button size="small">
            
              <AttachFile />
            </Button>
          </div>
        </div>
        <Button
          onClick={(e) => {
            if (groupChat) {
              openOptionsModal(e.currentTarget);
            } else {
              console.log("message send btn");
            }
            
          }}
          className={styles.sendBtn}
        >
          Send
        </Button>
      </div>
      {/* For web view */}
      <div className={styles.chatControls}>
        <div className={styles.topContainer}>
        {taggedUsers.length > 0 && <div className={styles.avatar} style={{display:'flex'}}>
        {taggedUsers.map(t=><img src={t.profilePic} alt={t.userName} />)}
       </div>}
       {selectuser && <div style={{maxHeight: '300px', overflow: 'auto'}}>
        
       
            
              {console.log('selectedchat',selectedchat)}
              {selectedchat.groupParticipants.map((e) => 
              { if(selectedchat.userId !== e.userId && !tagedPart.includes(e.userId))
                return (
                <MessageCard
                  //id={nanoid()}
                  profilePic={e?.profilePic}
                  userName={e?.userName}
                  message={e?.message}
                  //time={e?.time}
                  //numUnreadMessages={e?.numUnreadMessages}
                  //isOnline={e?.isOnline}
                  onClick={()=> {selectpart(e)}}
                />
              )})}
           
            </div>}
          <textarea
            value={message}
            placeholder="write a message"
            rows={1}
            onChange={(e) => {writemessage(e.target.value)}}
            className='one-2-one-chat-area'
          />
         
        </div>
        {emojiPicker && <div className={styles.emojiPickerReact}><Picker pickerStyle={{width: '100%', height: '230px'}} onEmojiClick={onEmojiClick} /></div>}

        <div className={styles.bottomContainer}>
          <div className={styles.leftWrapper}>
            <div className={styles.emojiIcon}>
              <IconButton size="small">
                <BsEmojiSmile onClick={() => toggleEmojis()} />
              </IconButton>
            </div>
            <div className={styles.attachFile}>
              <IconButton size="small"  onClick={() => OpenFileExplorer()}>
                <input
              id='input_file_attachment'
                type="file"
                onChange={(e) => uploadAttachment(e)}
                hidden
              />
                <AttachFile />
              </IconButton>
            </div>
          </div>
          <Button
            className={styles.sendBtn}
            onClick={(e) => {
              // if (groupChat) {
              //   openOptionsModal(e.currentTarget);
              // } else {
              //   console.log('message send btn');
              // }
              if(emojiPicker) {
                setEmojiPicker(false);
              }
              setMessage("");
              setchatuser(null);
              if(message.trim() !== '') {
                socket.emit("chatMessage", taggedUsers?.length > 0 ? `${message}#~~#${JSON.stringify(taggedUsers)}#~~#` : message);
                settaggedUsers([])
                setTagedPart([])
              }
            }}
          >
            Send
          </Button>
        </div>
      </div>
      {/* <Popover
        PaperProps={{
          style: {
            // width: 185,
            marginTop: "-.6em",
          },
        }}
        open={openSendOptions}
        onClose={() => {
          setMenuAnchorEl(undefined);
          setOpenSendOptions(false);
        }}
        anchorEl={menuAnchorEl}
        anchorOrigin={{ vertical: "top", horizontal: "left" }}
        transformOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        elevation={2}
      >
        <Box minWidth="150px">
          <MenuItem key="1">Send to all</MenuItem>
          <MenuItem key="2">Sent to selected</MenuItem>
        </Box>
      </Popover> */}
    </>
  );
};

export default ChatControls;
