import { Box } from '@material-ui/core';
import { ChangeEvent, useCallback, useState } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { EmailOrPhone } from '../ui/EmailOrPhone';
import { Loader } from '../ui/Loader';
import { persistUname } from '../../core/utils';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/Login.module.scss';
import { ResetPassword } from './ResetPassword';
import axios from 'axios';


export function ForgotPassword() {
	const [loading, setLoading] = useState(false);
	const [emailOrPhone, setEmailOrPhone] = useState('');
	const [hasPendingOTP, setHasPendingOTP] = useState(false);
	const [userId, setUserId] = useState('');
	

	const onSubmit = useCallback(
		(ev: ChangeEvent<HTMLFormElement>) => {
			ev.preventDefault();
			

			if (!Validators.isEmailOrPhone(emailOrPhone)) {
				toast.warn('Enter a valid email or your phone number');
				return;
			}

			setLoading(true);
			let postBody = {
				email: Validators.isEmail(emailOrPhone) ? emailOrPhone : undefined,
				phone: Validators.isPhone(emailOrPhone) ? emailOrPhone : undefined,
			  };
			axios.post('/api/auth/check',postBody).then(res => {
				if(res.data !== 'User not found') {

				  setLoading(false);
				  toast.success(__tr('pleaseEnterOtp'));
				  setUserId(res.data);
			 	  setHasPendingOTP(true);
				   if(postBody.phone) {
					const mob = postBody.phone.split(" ");
					if(mob.length === 2) {
					axios.post('/api/auth/otp/send',{isPhone:true,countryCode:mob[0],number:mob[1]}).then(res=> {
					  setLoading(false);
						toast.success(__tr('msgOtpCodeSent'));
					});
				  }
				} else {
				  axios.post('/api/auth/otp/send',{isPhone:false,email:postBody.email,name:res.data.name}).then(res=> {
					  setLoading(false);
						toast.success(__tr('msgOtpCodeSent'));
					});
				}
				   
				} else {
				  setLoading(false);
				  setHasPendingOTP(false);
			      toast.error('User not fournd');
				}
			  
			})
		},
		[emailOrPhone]
	);

	if (hasPendingOTP) {
		return <ResetPassword user={userId} emailorPhone={emailOrPhone}/>;
	}

	return (
		<div className={styles.loginWrapper}>
			<div className={styles.formWrapper}>
				<h3>{__tr('forgotPassword')}</h3>
				<p className={styles.subtitle}>{__tr('pleaseEnterEmail')}</p>
				<form action='' onSubmit={onSubmit} className={styles.form}>
					{loading && <Loader />}
					<Box marginY={2} marginTop={2}>
						<EmailOrPhone
							label={__tr('labelEmailOrPassword')}
							placeholder='john@example.com'
							onChange={val => setEmailOrPhone((val.value).toLowerCase())}
						/>
					</Box>
					<Box marginY={2}>
						<button type='submit' className={styles.btnSubmit}>
							{__tr('btnResetPassword')}
						</button>
					</Box>
					<Box
						marginY={1}
						display='flex'
						flexDirection='row'
						alignItems='center'
						justifyContent='center'
					>
						<Link to={Routes.Login} className={styles.signupLink}>
							{__tr('back')}
						</Link>
					</Box>
				</form>
			</div>
		</div>
	);
}
