import {
  Avatar,
  IconButton,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  Box,
  Popover,
} from "@material-ui/core";
import { toast } from "react-toastify";
import { __tr } from "../../i18n";
import { MdPerson, MdMoreVert } from "react-icons/md";
import { Link } from "react-router-dom";
import "../../styles/CampaignTable.module.scss";
import { Routes } from "../../routes";
import { usePostedCampaigns } from "../../state/selectors";
import { CssVariables } from "../../css-variables";
import WhatsappSvg from "../../assets/fram.svg";
import { setSelectedCampaign } from "../../state/action-creators";
import { useDispatch } from "react-redux";
import { useAppUser } from "../../state/selectors";
import { useState } from "react";
import axios from "axios";
import { setCampaigns } from "../../state/action-creators";
import { Campaign } from "../../models/index";
import {ShareCampaignAccess} from "./ShareCampaignAccess";

interface Props {
  statusFlag: any;
}
export function PostedCampaignTable(props: Props) {
  const postedCampaigns = usePostedCampaigns();
  const [openOptions, setOpenOptions] = useState<boolean>(false);
  const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement>();
  const [selectedItem, setSelectedItem] = useState<any>();
  const [dataFilter, setdataFilter] = useState<any>();

  const dispatch = useDispatch();
  const { user } = useAppUser();
  const handleOptionsModalOpen = (el: HTMLElement, item: any) => {
    setMenuAnchorEl(el);
    setOpenOptions(true);
    setSelectedItem(item);
  };

  //open options modal
  const handleOptionsModalClose = () => {
    setMenuAnchorEl(undefined);
    setOpenOptions(false);
  };
  const token = localStorage.getItem("token");
  const getCampaign = () => {
    if (token) {
      axios
        .get("/api/campaign/" + user._id, { headers: { Authorization: token } })
        .then((res) => {
          const list: Campaign[] = res.data ?? [];
          dispatch(setCampaigns(list));
        });
    }
  };

  // .sort(
  //   (p1, p2) => Date.parse(p2.createdAt || "") - Date.parse(p1.createdAt || "")
  // );

  //const postedCampaigns = [{id:'12asda',Name:"Test cam",CampaignStatus:'Active'}]

  const StatusChange = (input: any, id: any) => {
    const updateCampaginStatus = {
      status: input,
      campaignId: id,
    };

    if (token) {
      console.log(updateCampaginStatus);
      axios

        .post(`/api/campaign/changeStatus/`, updateCampaginStatus, {
          headers: { Authorization: token },
        })
        .then((res: any) => {
          console.log(res.data);
          getCampaign();
          toast.success("Status changed successfully");
        })
        .catch((err: any) => {
          console.log("err", err.response.data);
          toast.error("Something went wrong!");
        });
    }
    setOpenOptions(false);
  };

  return (
    <div className="section">
      <TableContainer>
        <Table style={{ width: "100%" }}>
          <TableHead style={{ width: "100%" }}>
            <TableRow>
              <TableCell></TableCell>
              <TableCell>
                <label style={{ fontWeight: "600" }} className="headline">
                  {__tr("campaigns")}
                </label>
              </TableCell>
              <TableCell>
                <label style={{ fontWeight: "600" }} className="headline">
                  {__tr("status")}
                </label>
              </TableCell>
              <TableCell>
                <label style={{ fontWeight: "600" }} className="headline">
                  {__tr("applicants")}
                </label>
              </TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          {postedCampaigns.length > 0 ? (
            <TableBody>
              {postedCampaigns
                ?.filter(
                  (x) =>
                    props.statusFlag
                      ? x.CampaignStatus?.toLowerCase() ===
                      props.statusFlag.toLowerCase()
                      : 1 == 1

                  // console.log(x.CampaignStatus?.toLowerCase(), props.statusFlag)
                )
                .map((p, key) => {
                  console.log('p',p);
                  return (
                    <TableRow key={p?._id}>
                      <TableCell>
                        <Avatar  src={p?.brand?.brandImageUrl} />
                          {/* <MdPerson size={18} /> */}
                          {/* {p.Name.charAt(0).toUpperCase()} */}
                        {/* </Avatar> */}
                      </TableCell>
                      <TableCell>
                        <div className="campaignTitleRow">
                          <Typography variant="body1">
                            {p.Name || ""}
                          </Typography>
                        </div>
                      </TableCell>
                      <TableCell>
                        {/* {__tr(`status_${p.CampaignStatus?.toLowerCase() || ""}`)} */}
                        {p.CampaignStatus === "SUBMITTED"
                          ? "PENDING"
                          : p.CampaignStatus}
                        {/* {p.CampaignStatus || 0} */}
                      </TableCell>
                      <TableCell>
                        {console.log(selectedItem?.CampaignStatus,)}
                        <Link
                          to={p.CampaignStatus === "APPROVED" || p.CampaignStatus === "CLOSED" ? Routes.viewCampaignApplicants(p?.campaignId || "") : Routes.EditCampaign(
                            p?._id || ""
                          )}
                          style={{
                            fontWeight: 500,
                            color: CssVariables.colorPrimary,
                          }}
                          onClick={() => {
                            dispatch(setSelectedCampaign(p));
                          }}
                        >
                          {p.CampaignStatus === "APPROVED" || p.CampaignStatus === "CLOSED"
                            ? "View"
                            : "EDIT"}
                          {/* View({p.CampaignStatus || 0}) */}
                        </Link>
                      </TableCell>
                      <TableCell>
                        {/* <Link to={Routes.UpdateCampaigns + p?._id}>
                      <IconButton size="small">
                        <MdMoreVert size={24} />
                      </IconButton>
                      </Link> */}

                        <IconButton
                          onClick={(ev: React.MouseEvent<HTMLElement>) => {
                            handleOptionsModalOpen(ev.currentTarget, p);
                          }}
                        >
                          <MdMoreVert size={24} />
                        </IconButton>

                        {selectedItem && (
                          <Popover
                            open={openOptions}
                            onClose={handleOptionsModalClose}
                            anchorEl={menuAnchorEl}
                            anchorOrigin={{
                              vertical: "bottom",
                              horizontal: "left",
                            }}
                            elevation={2}
                          >

                            <Box minWidth="150px">
                              {selectedItem?.CampaignStatus === "SUBMITTED" || selectedItem?.CampaignStatus === "PAUSED" || selectedItem?.CampaignStatus === "PRIVATE" ||
                                selectedItem?.CampaignStatus === "DRAFT" ||
                                user?.role === "ADMIN" ? (
                                <MenuItem>
                                  <Link
                                    to={Routes.EditCampaign(
                                      selectedItem?._id || ""
                                    )}
                                  >
                                    <span>Edit Campaign</span>
                                  </Link>
                                </MenuItem>
                              ) : (
                                <></>
                              )}
                              {selectedItem?.CampaignStatus !== "SUBMITTED" && selectedItem?.CampaignStatus !== "CLOSED" && selectedItem?.CampaignStatus !== "DRAFT" ? selectedItem.CampaignStatus == "PAUSED" ? (<MenuItem
                                value="RESUME"
                                onClick={() =>
                                  StatusChange(
                                    "APPROVED",
                                    selectedItem?._id || ""
                                  )
                                }
                              >
                                Resume Campaign
                              </MenuItem>) : (<MenuItem
                                value="PAUSED"
                                onClick={() =>
                                  StatusChange(
                                    "PAUSED",
                                    selectedItem?._id || ""
                                  )
                                }
                              >
                                Pause Campaign
                              </MenuItem>) : ""}

                              {selectedItem?.CampaignStatus !== "CLOSED" && selectedItem?.CampaignStatus !== "DRAFT" ? selectedItem?.CampaignStatus === "PRIVATE" ? (
                                <MenuItem
                                  value="PUBLIC"
                                  onClick={() =>
                                    StatusChange(
                                      "PUBLIC",
                                      selectedItem?._id || ""
                                    )
                                  }
                                >
                                  <span>Make Campaign Public</span>
                                </MenuItem>
                              ) : (
                                <MenuItem
                                  value="PRIVATE"
                                  onClick={() =>
                                    StatusChange(
                                      "PRIVATE",
                                      selectedItem?._id || ""
                                    )
                                  }

                                >
                                  <span>Make Campaign Private</span>
                                </MenuItem>
                              ) : ""}

                              <MenuItem
                                value="CLOSED"
                                onClick={() =>
                                  StatusChange(
                                    "CLOSED",
                                    selectedItem?._id || ""
                                  )
                                }
                              >
                                <span>Close Campaign </span>
                              </MenuItem>
                              {selectedItem?.CampaignStatus !== "DRAFT" && (< MenuItem>
                                <Link
                                  to={Routes.PostSimilarCampaign(
                                    selectedItem?._id || ""
                                  )}
                                >
                                  <span>Post Similar Campaign </span>
                                </Link>
                              </MenuItem>
                              )}
                               <MenuItem>
                               <span><ShareCampaignAccess user={selectedItem?._id } /></span>
                              </MenuItem>
                            </Box>
                          </Popover>
                        )}
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          ) : (
            <TableBody>
              <TableRow>
                <TableCell colSpan={12}>
                  <div
                    style={{
                      alignItems: "center",
                      justifyContent: "center",
                      textAlign: "center",
                      display: "flex",
                      width: "100%",
                    }}
                  >
                    <img
                      style={{ alignItems: "center", justifyContent: "center" }}
                      src={WhatsappSvg}
                      alt="Logo"
                    />
                  </div>
                </TableCell>
              </TableRow>
            </TableBody>
          )}
        </Table>
      </TableContainer>
    </div>
  );
}
