import React, { useState, useEffect } from "react";
import leftImg from "../../../assets/images/left.png";
import rightImg from "../../../assets/images/right.png";

function Pagination({ showPerPage, onPageChange, total }) {
  const [counter, setCounter] = useState(1);
  useEffect(() => {
    const value = showPerPage * counter;
    onPageChange(value - showPerPage, value);
  }, [counter]);

  const onButtonClick = (type: any) => {
    if (type === "prev") {
      if (counter === 1) {
        setCounter(1);
      } else {
        setCounter(counter - 1);
      }
    } else if (type === "next") {
      if (Math.ceil(total / showPerPage) === counter) {
        setCounter(counter);
      } else {
        setCounter(counter + 1);
      }
    }
  };
  return (
    <div
      style={{
        marginTop: "1rem",
        marginLeft: "34.8rem",
        fontWeight: "300",
        fontSize: "0.9rem",
        marginBottom: "1rem",
      }}
    >
      <span style={{ marginRight: "3rem" }}>Rows per page : {showPerPage}</span>
      <span style={{ marginRight: "3rem" }}>
        {counter}-{Math.ceil(total / showPerPage)} of {total}
      </span>
      <button
        style={{ marginRight: "2rem", background: "white", border: "none" }}
        onClick={() => onButtonClick("prev")}
      >
        <img
          src={leftImg}
          style={{
            width: "15px",
            height: "15px",
            objectFit: "contain",
          }}
        />
      </button>

      <button
        style={{ background: "white", border: "none" }}
        onClick={() => onButtonClick("next")}
      >
        <img
          src={rightImg}
          alt="next"
          style={{
            width: "15px",
            height: "15px",
            objectFit: "contain",
          }}
        />
      </button>
    </div>
  );
}

export default Pagination;
