import { Box } from '@material-ui/core';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useHistory } from 'react-router';
import { PropagateLoader } from 'react-spinners';
import { toast } from 'react-toastify';
import { OTPInput } from '../ui/OTPInput';
import { userFromUname } from '../../core/utils';
import { CssVariables } from '../../css-variables';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/ConfirmOtp.module.scss';
import axios from 'axios';

interface Props {
  signIn(): any;
  user?: any;
}

export function ConfirmOTP(props: Props) {
  const [loading, setLoading] = useState(false);

  const history = useHistory();

  // useEffect(() => {
  //   if (!user) {
  //     history.replace(Routes.Signup);
  //   }
  // }, [user, history]);

  console.log('props',props.user)

  const onResendOtp = useCallback(() => {
    if (loading) {
      return;
    }
    setLoading(true);

    if(props.user.phone) {
      const mob = props.user.phone.split(" ");
      if(mob.length === 2) {
      axios.post('/api/auth/otp/send',{isPhone:true,countryCode:mob[0],number:mob[1]}).then(res=> {
        setLoading(false);
          toast.success(__tr('msgOtpCodeSent'));
      });
    }
  } else {
    axios.post('/api/auth/otp/send',{isPhone:false,email:props.user.email,name:props.user.name}).then(res=> {
        setLoading(false);
          toast.success(__tr('msgOtpCodeSent'));
      });
  }
  }, [loading]);

  const confirmOTP = useCallback(
    (otp: string) => {
      if (loading) {
        return;
      }

      if (otp) {
        setLoading(true);

        if(props.user.phone) {
        const mob = props.user.phone.split(" ");
        if(mob.length === 2) {
        axios.post('/api/auth/otp/verify',{isPhone:true,countryCode:mob[0],number:mob[1],otp,userId:props.user._id})
        .then(res=> {
          console.log(res);
          setLoading(false);
            /// account verification is now complete.
            toast.success(__tr('msgAccountCodeVerified'));
            props.signIn();
            //history.replace(Routes.Home);
          
        }).catch( err=>{
          toast.error("Otp not Matched")
          setLoading(false)
          console.log('eeeeeeeeeee',err)
        })
        
        
        ;}
        else {
          toast.error("Incorrect Mobile Number");
        }
      } else {
        axios.post('/api/auth/otp/verify',{isPhone:false,otp,userId:props.user._id}).then(res=> {
            
        setLoading(false);
            /// account verification is now complete.
            toast.success(__tr('msgAccountCodeVerified'));
            props.signIn();
            //history.replace(Routes.Home);
          
        }).catch( err=>{
          toast.error("Otp not Matched")
          setLoading(false)
          console.log('eeeeeeeeeee',err)
        });
      }
      }
    },
    [loading, history]
  );

  return (
    <div className={styles.loginWrapper}>
      <h3>{__tr('welcomeToWobb')}</h3>
      <div className={styles.formWrapper}>
        <h3>{props.user.phone ? 'Verify Phone Number' : 'Verify Email ID'}</h3>
        <p className={styles.subtitle}>Please enter the OTP sent to {props.user.phone ? ' Mobile Number' : ' Email ID'}</p>
        <form action='' className={styles.form}>
          <Box marginY={2}>
            <OTPInput length={6} onChange={confirmOTP} />
          </Box>
          {loading && (
            <Box
              marginY={2}
              width={'100%'}
              display='flex'
              flexDirection='row'
              alignItems='center'
              justifyContent='center'
            >
              <PropagateLoader size={12} color={CssVariables.colorPrimary} />
            </Box>
          )}
          <Box
            marginY={2}
            marginTop={6}
            display='flex'
            flexDirection='column'
            alignItems='center'
            justifyContent='center'
          >
            <button type='button' className={styles.resendBtn} onClick={onResendOtp}>
              {__tr('resendOtp')}
            </button>
          </Box>
        </form>
      </div>
    </div>
  );
}
