interface Props {
  requirements?: InfluencerRequirements;
  onChange(value: InfluencerRequirements): any;
}

export function InfluencerRequirementAdvancedFilter(props: Props) {
  const [disabled, setdisabled] = useState(false);
  const [ageRange, setAgeRange] = useState([
    props.requirements?.age.from || 18,
    props.requirements?.age.to || 35,
  ]);
  const [country, setCountry] = useState(props.requirements?.country || "");
  const [city, setCity] = useState(props.requirements?.city || "");
  const [gender, setGender] = useState();
  const [language, setLanguage] = useState(props.requirements?.language || []);
  const [location, setLocation] = useState(props.requirements?.location || []);

  const { onChange } = props;

  useEffect(() => {
    let filter: InfluencerRequirements = {
      age: {
        from: ageRange[0],
        to: ageRange[1],
      },
      country,
      city,
      gender,
      language,
      location,
    };
    onChange(filter);
  }, [onChange, country, ageRange, city, gender, language, location]);

  const onRemoveLanguage = (add: string) => {
    const idx = language.indexOf(add);
    const lang: typeof language = [];
    language.forEach((a, i) => {
      if (i !== idx) {
        lang.push(a);
      }
    });
    setLanguage(lang);
  };
  // const [value, setValue] =useState('female');

  // const handleChange = (event:any) => {
  //   setValue(event.target.value);
  // };
  const { id }: { id: string } = useParams();
 const [campaign,setCampign]=useState([]);
  useEffect(() => {
    if (token && id !== undefined) {
      axios
        .get(`/api/campaign/id/${id}`, { headers: { Authorization: token } })
        .then((result) => {
          console.log("hii", result);
          if (result.data[0].Gender !== undefined) {
            setCampign(result.data[0]);
            setGender(result.data[0].Gender);
            setLanguage(result.data[0].language);
            //  setLocation(result.data[0].location)
            console.log("gender", result.data[0].Gender);
            console.log("lan", result.data[0].location);
            setAgeRange(result.data[0].minAge);
            setAgeRange(result.data[0].maxAge);
          }
        });
    }
  }, []);
  console.log("cam",campaign.Gender)
  if(id!==undefined ){
    if(campaign.Gender!==undefined){
      if(!gender){
      return <CircularProgress />
      }
    } 
  }
  return (
    <Box width="100%">
      <Grid container spacing={2} >
        <Grid item xs={12}>
        <Box marginY={0.5}>
        <InputLabel>{__tr("age")}</InputLabel>
      </Box>
      <Box marginY={1} marginX={2}>
        <StyledSlider
          min={18}
          value={ageRange}
          valueLabelDisplay="auto"
          onChange={(ev, val) => setAgeRange(val as number[])}
          max={100}
          marks={marks}
        />
      </Box>
      </Grid>
      <Grid item xs={4}>
      <Box marginY={0.5}>
        <InputLabel>{__tr("gender")}</InputLabel>
      </Box>
      <Box marginY={1}>
        <TextField
          value={gender}
          onChange={(ev) => setGender(ev.target.value as Gender)}
          select
          size="small"
          placeholder={__tr("gender")}
          fullWidth
          variant="outlined"
        >
          <MenuItem ></MenuItem>
          <MenuItem value={Gender.MALE}>{__tr("male")}</MenuItem>
          <MenuItem value={Gender.FEMALE}>{__tr("female")}</MenuItem>
          <MenuItem value={Gender.OTHERS}>{__tr("others")}</MenuItem>
         
        </TextField>
        {gender=== undefined && <p  style={{ marginLeft: '11px', marginTop: '-29px', color: 'rgb(187, 187, 187',fontFamily:'poppins',fontWeight:400,fontSize:'14px'}}>
         Gender

        </p>}        
      </Box>
      </Grid>
      <Grid item xs={4} className="location-grid">
     <Box marginY={0.5}>
        <InputLabel>{__tr("location")}</InputLabel>
      </Box>
      <Box marginY={1} style={{width:'100%'}}>
        <Location onChange={(val: string[]) => setLocation(val)} />

        {/* <TextField
          value={country}
          onChange={(ev) => setCountry(ev.target.value)}
          select
          fullWidth
          size='small'
          variant='outlined'
          placeholder={__tr('country')}
        >
          <MenuItem value='IND'>India</MenuItem>
          <MenuItem value='PAK'>Pakistan</MenuItem>
          <MenuItem value='USA'>United States</MenuItem>
        </TextField> */}
      </Box>
      {/* <Box marginY={0.5}>
        <InputLabel>{__tr('city')}</InputLabel>
      </Box>
      <Box marginY={1}>
        <TextField
          fullWidth
          size='small'
          variant='outlined'
          placeholder={__tr('city')}
          value={city}
          onChange={(ev) => setCity(ev.target.value)}
        />
      </Box> */}
      </Grid>
      <Grid item xs={4}>
      <Box marginY={0.5}>
        <InputLabel>{__tr("language")}</InputLabel>
      </Box>
      <Box marginY={1}>
        
        <TextField
          select
          fullWidth
          size="small"
          variant="outlined"
          placeholder="Enter language"
          value={'Select Language'}
          onChange={(ev) => {
            if(language?.length > 0){
              const isAlreadySelected = language?.findIndex(a=>a === ev.target.value);
              console.log('isAlreadySelected', isAlreadySelected)
              if(isAlreadySelected === -1) {
                setLanguage([...language, ev.target.value])
              }
            } else {
              setLanguage([...language, ev.target.value])
            }
            
          }}
        >
          {languageList.map((l) => {
            return (
              <MenuItem value={l}>
                {l}
              </MenuItem>
            );
          })}
        </TextField>
         {language.length === 0 && <p  style={{ marginLeft: '11px', marginTop: '-29px', color: 'gray'}}>
          Select Language
        </p>}
        {language.map((c) => {
          return (
            <Chip
              size="small"
              variant="outlined"
              color="primary"
              label={c}
              clickable
              onDelete={() => onRemoveLanguage(c)}
              style={{ marginLeft: "4px", marginTop: "5px" }}
            />
          );
        })}
      </Box>
      </Grid>
      </Grid>
    </Box>
  );
}
