import { createTheme, CssBaseline, ThemeProvider } from "@material-ui/core";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import { Routes } from "./routes";
import { AuthLayout } from "./components/auth/AuthLayout";
import { Home } from "./components/Home";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { CssVariables } from "./css-variables";
import { RouteProtector } from "./components/auth/RouteProtector";
import { Provider } from "react-redux";
import { store } from "./state/store";
import BaseLanding from "./components/landing/pages/BaseLanding";
import Initializer from "./components/ui/Initializer";
import UserContext from "./contexts/UserContext";
import { useEffect, useState } from "react";
import { User } from "./models";
import axios from "axios";
import { useLocation } from "react-router-dom";

import CampaignsDiscovery from "./components/landing/component/CampaignsDiscovery";

axios.defaults.baseURL = "https://devapi.wobb.ai";

axios.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (401 === error?.response?.status) {
      localStorage.clear();
      window.location.href = Routes.Home;
    } else {
      throw error;
    }
  }
);
const appTheme = createTheme({
  palette: {
    primary: {
      main: "#012FB7",
      dark: "#080C4E",
    },
    text: {
      primary: "#333333",
      secondary: "#484848",
      hint: "#646464",
      disabled: "#848484",
    },
  },
  typography: {
    fontFamily: "Poppins",
  },
});

appTheme.shadows[1] = `1px 1px 2px ${CssVariables.colorGrayV1}`;
appTheme.shadows[2] = `1px 1px 4px ${CssVariables.colorGrayV1}`;

function App() {
  const [user, setUser] = useState<User>({
    _id: "",
    name: "",
    email: "",
    uid: "",
    role: "INFLUENCER",
    imageUrl: "",
  });
  const location = useLocation();
  useEffect(() => {
    if (location) window.analytics.page(location.pathname);
  }, [location]);

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      axios
        .get("/api/user/info", { headers: { Authorization: token } })
        .then((res) => {
          setUser(res?.data?.user);
        });
    }
  }, []);

  return (
    <UserContext.Provider value={{ user, setUser }}>
      <Provider store={store}>
        <ThemeProvider theme={appTheme}>
          <CssBaseline />
          <BrowserRouter>
            <Initializer>
              <Switch>
                <Route path={Routes.Home}>
                  <RouteProtector>
                    <Home />
                  </RouteProtector>
                </Route>
                <Route path={Routes.Auth}>
                  <AuthLayout />
                </Route>
                <Route path={Routes.Base}>
                  <BaseLanding />
                </Route>
              </Switch>
            </Initializer>
          </BrowserRouter>
          <ToastContainer
            position="bottom-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={true}
            closeOnClick={true}
            pauseOnFocusLoss
            pauseOnHover
          />
        </ThemeProvider>
      </Provider>
    </UserContext.Provider>
  );
}

export default App;
