import {
  Box,
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  Typography,
} from '@material-ui/core';
import { toast } from "react-toastify";
import { withStyles } from '@material-ui/styles';
import { useState } from 'react';
import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { Link, NavLink, useHistory } from 'react-router-dom';
import { Stars } from '../ui/Stars';
import { TextTransformNoneButton } from '../ui/TextTransformNoneButton';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import { setCurrentUser } from '../../state/action-creators';
import { useAppUser } from '../../state/selectors';
import styles from '../../styles/ProfileCard.module.scss';
import EditIcon from '@material-ui/icons/Edit';
import axios, { AxiosResponse } from "axios";
import { Label } from '@material-ui/icons';

const RoundedButton = withStyles({
  root: {
    borderRadius: 32,
  },
})(Button);

export function ProfileNavigationCard() {
  const [askingSignOut, setAskingSignout] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { user } = useAppUser();
  const history = useHistory();
  const dispatch = useDispatch();
  const [imageUrl, setimageUrl] = useState();
  const [image, setimage] = useState<any>();
  const viewProfile = useCallback(() => {
    history.push(Routes.MyProfile);
  }, [history]);

  const handlerInputChange = (event: any) => {
    let files = event.target.files;
    console.log(event.target);
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (e: any) => {
      updateImage(e.target?.result);
    };
    // let files = event.target.files;
    // let reader = new FileReader();
    // reader.readAsDataURL(files[0]);
    // reader.onload = (e: any) => {
    //   // const url =  reader.readAsDataURL(files[0]);
    //   // document.getElementById("userProfile").src=url
    //   setimage(e.target?.result );
    // };
  };

  const img=user?.imgUrl;
  const token = localStorage.getItem("token");
  const userImg = user?.imgUrl ? user.imgUrl :'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png';
  
  const updateImage = (files: any) => {
    const formData = { file: files, name: user?.name };
    setIsLoading(true);
    if (token) {
      axios
        .post(`/api/s3/upload`, formData, {
          headers: { Authorization: token },
        }).then(updres=> {
          const updatelogo = { _id:user._id,imgUrl: updres.data};
      
            axios
              .put("https://devapi.wobb.ai/api/user/image", updatelogo, {
                headers: { Authorization: token },
              })
              .then((res) => {
                axios
                .get("/api/user/info", { headers: { Authorization: token } })
                .then((res2) => {
                  dispatch(setCurrentUser(res2.data.user));
                  setIsLoading(false);
                  toast.success("Profile picture updated Successfully")
                });
                // console.log(res.data);
                // document.getElementById("userProfile").src=URL.createObjectURL(file)
              })
              .catch((err) => {
                console.log("err", err.response.data);
                setIsLoading(false);
                toast.error("Something went wrong!")
              });
          
          
        }).catch((err) => {
          console.log("err", err.response.data);
          setIsLoading(false);
          toast.error("Something went wrong!")
        });
      }


   
  };

  const onSignOut = useCallback(async () => {
    setAskingSignout(false);
    
    localStorage.removeItem('picture');
    localStorage.removeItem('token');
    /// unset the user in app state.
    dispatch(setCurrentUser(undefined));
    history.replace(Routes.Base);
  }, [history, dispatch]);

  if (!user) {
    return <></>;
  }
  return (
    <Box padding={1} className={styles.page}>
      <Box padding={2} className={styles.profile}>
        <div className={styles.image}>
          <img
            className={styles.image}
            src={userImg}
            alt='profile'
            id="userProfile"
          />
          {/* <label htmlFor='select-image' ><EditIcon color='primary' style={{marginTop:"32px",marginRight:"12px"}}/></label> */}
            <input
             type="file"
            name="select-image"
            id="select-image"
            accept="image/png, image/gif, image/jpeg"
            onChange={handlerInputChange}
            style={{ display: "none" }}
          />
        </div>
        <div className={styles.content}>
          <Typography variant='h6' className={styles.name}>
            {user.name}
          </Typography>
          <Typography variant='body2' className={styles.sub}>
           {user.role}
          </Typography>
        </div>
      </Box>
      <Box padding={2} className={styles.buttons}>
        <RoundedButton  onClick={()=>{ document.getElementById("select-image").click()}} color='primary' fullWidth>
          {isLoading ? <CircularProgress /> : 'change your profile picture'}
        </RoundedButton>
      </Box>
      <Box padding={1} className={styles.navigation}>
        {/* <NavLink to={Routes.MyEarnings} className={styles.navItem} activeClassName={styles.navItemActive}>
          {__tr('myEarnings')}
        </NavLink>
        <NavLink to={Routes.MyOrders} className={styles.navItem} activeClassName={styles.navItemActive}>
          {__tr('myOrders')}
        </NavLink>
        <NavLink to={Routes.Affiliate} className={styles.navItem} activeClassName={styles.navItemActive}>
          {__tr('referAndEarn')}
        </NavLink>
        <NavLink to={Routes.UserManagement} className={styles.navItem} activeClassName={styles.navItemActive}>
          {__tr('userManagement')}
        </NavLink> */}
        <NavLink
          to={Routes.PrivacyPolicies}
          className={styles.navItem}
          activeClassName={styles.navItemActive}
        >
          <Link  to="/privacy-policy">
          {__tr('privacyPolicy')}
          </Link>
        
        </NavLink>
        <NavLink to={Routes.HelpAndSupport} className={styles.navItem} activeClassName={styles.navItemActive}>
          {__tr('helpAndSupport')}
        </NavLink>
        <span role='button' className={styles.navItem} onClick={() => setAskingSignout(true)}>
          {__tr('signOut')}
        </span>
      </Box>
      <Dialog open={askingSignOut} onClose={() => setAskingSignout(false)} maxWidth='sm' fullWidth>
        <DialogTitle>Exit</DialogTitle>
        <DialogContent>
          <DialogContentText>Please confirm again to exit.</DialogContentText>
        </DialogContent>
        <DialogActions>
          <TextTransformNoneButton variant='outlined' color='primary' onClick={() => setAskingSignout(false)}>
            Cancel
          </TextTransformNoneButton>
          <TextTransformNoneButton variant='contained' color='primary' onClick={onSignOut}>
            Yes, exit
          </TextTransformNoneButton>
        </DialogActions>
      </Dialog>
    </Box>
  );
}
