import {
  Box,
  Divider,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemSecondaryAction,
  ListItemText,
  Radio,
  TextField,
} from "@material-ui/core";
import axios from "axios";
import { useCallback, useEffect } from "react";
import { useMemo } from "react";
import { useState } from "react";
import { MdAdd, MdEdit } from "react-icons/md";
import { TextTransformNoneButton } from "../TextTransformNoneButton";
import { __tr } from "../../../i18n";
import { Brand } from "../../../models/index";
import { useAppUser, useBrands } from "../../../state/selectors";
import { useDispatch } from "react-redux";
import { appendBrand, setBrands } from "../../../state/action-creators";
import { MdDelete } from "react-icons/md";

interface Props {
  brand?: Brand;
  onChange(value: Brand): any;
  userId: string;
}

export function BrandPicker(props: Props) {
  const [name, setBrandName] = useState("");
  const [website, setBrandWebsite] = useState("");
  const [busy, setBusy] = useState(false);
  const token = localStorage.getItem("token");
  const { user } = useAppUser();
  const brands = useBrands();
  const dispatch = useDispatch();

  const [adding, setAdding] = useState(false);
  const [editing, setEditing] = useState(false);
  const [updateCandidate, setUpdateCandidate] = useState<Brand>();

  const brandsWithoutUpdate = useMemo(() => {
    if (!editing && !updateCandidate) {
      return brands;
    }
    return brands.filter((b) => b.brandId !== updateCandidate?.brandId);
  }, [editing, updateCandidate, brands]);

  useEffect(() => {
    axios
      .get("/api/brand/" + user._id, { headers: { Authorization: token } })
      .then((res) => {
        dispatch(setBrands(res.data));
      });
  }, [token]);

  const cancelAddOrUpdate = useCallback(() => {
    setAdding(false);
    setUpdateCandidate(undefined);
    setEditing(false);
  }, []);

  const selectForUpdate = useCallback((brand: Brand) => {
    setUpdateCandidate(brand);
    setBrandWebsite(brand.website || "");
    setBrandName(brand.name || "");
    setEditing(true);
  }, []);

  const onAppendBrand = useCallback(async () => {
    setBusy(true);
    let brand = {
      name,
      website,
      userId: user._id,
    };

    axios
      .post("/api/brand", brand, { headers: { Authorization: token } })
      .then((res) => {
        dispatch(appendBrand(res.data));
        setBrandName("");
        setBrandWebsite("");
        setBusy(false);
        props.onChange(res.data);
      });
  }, [dispatch, name, website, user, props, brands]);

  const onUpdateBrand = useCallback(async () => {
    if (updateCandidate) {
      setBusy(true);
      console.log("updateCandidate", updateCandidate);
      let brand = {
        name,
        website,
        userId: props.userId,
      };

      axios
        .put("/api/brand/" + updateCandidate._id, brand, {
          headers: { Authorization: token },
        })
        .then((res) => {
          console.log("ressssss", res);
          dispatch(appendBrand(res.data));
          setBrandName("");
          setBrandWebsite("");
          setUpdateCandidate(undefined);
          setEditing(false);
          setBusy(false);
        });
    }
  }, [dispatch, name, updateCandidate, website]);

  const onRadioChange = useCallback(
    (brand: Brand, checked: boolean) => {
      if (checked) {
        props.onChange(brand);
      }
    },
    [props]
  );
  function validateUrl(value:any) {
    return /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(value);
  }
  const IsUrlInvalid = validateUrl(website);

   
  return (
    <Box >
      {!adding && (
        <>
          <Box paddingX={2} paddingY={0.5}>
            <TextTransformNoneButton
              variant="text"
              color="primary"
              size="small"
              startIcon={<MdAdd size={16} />}
              onClick={() => setAdding(true)}
            >
              {__tr("btnAddNewBrand")}
            </TextTransformNoneButton>
          </Box>
          <Divider />
        </>
      )}
      {(adding || editing) && (
        <Box padding={1}>
          <Box marginY={0.75}>
            <TextField
              size="small"
              variant="outlined"
              placeholder={__tr("enterBrandName")}

              value={name}
              onChange={(ev) => setBrandName(ev.currentTarget?.value)}
              fullWidth
            />
          </Box>
          <Box marginY={0.75}>
            <TextField
              size="small"
              variant="outlined"
              type="url"
              inputProps={{ pattern: "https://.*" }}
              placeholder={__tr("enterBrandWebsite")}

              value={website}
              helperText={!website || !IsUrlInvalid ?"enter proper url with https ":""}
              // error={website.length>0||!!IsUrlInvalid ?"please enter valid url":null}
              onChange={(ev) => setBrandWebsite(ev.currentTarget?.value)}
              fullWidth
            />
          </Box>
          <Box
            paddingY={0.5}
            display="flex"
            flexDirection="row"
            alignItems="center"
            justifyContent="center"
          >
            <Box marginX={1}>
              <TextTransformNoneButton
                variant="contained"
                color="primary"
                size="small"
                onClick={updateCandidate ? onUpdateBrand : onAppendBrand}
                disabled={(name.length<2||name.length>30) || (website.length>0?!IsUrlInvalid:"" ) }
              >
                {__tr("save")}
              </TextTransformNoneButton>
            </Box>
            <Box marginX={1}>
              <TextTransformNoneButton
                variant="outlined"
                color="default"
                size="small"
                onClick={cancelAddOrUpdate}
              >
                {__tr("cancel")}
              </TextTransformNoneButton>
            </Box>
          </Box>
        </Box>
      )}
      <List dense disablePadding>
        {brandsWithoutUpdate.map((b) => {
          return (
            <label
              key={b}
              style={{
                cursor: "pointer",
                display: "block",
              }}
            >
              <ListItem dense button key={b.name}>
                <ListItemAvatar>
                  <Radio
                    color="primary"
                    size="small"
                    value={b.brandId}
                    checked={props.brand?.brandId === b.brandId}
                    onChange={(ev, check) => onRadioChange(b, check)}
                  />
                </ListItemAvatar>
                <ListItemText primary={b.name} />
                <ListItemSecondaryAction>
                  <IconButton size="small" onClick={() => selectForUpdate(b)}>
                    <MdEdit size={16} />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            </label>
          );
        })}
      </List>
    </Box>
  );
}
