function CampaignHeader({ data }) {
  const getPayoutValue = (payout) => {
    switch (payout?.type) {
      case "FIXED":
        return payout?.cash?.amount;
      case "BARTER":
        return payout?.productMRP?.amount;
      case "VARIABLE":
        return payout?.maxCash?.amount;
      case "CPA":
        return payout?.details?.rate;
    }
  };
  return (
    <>
      <Avatar
        style={{
          width: "4rem",
          height: "4rem",
          background: "#FFF",
          border: "1px solid #DDDDDD",
          boxSizing: "border-box",
          borderRadius: "8px",
          top: "-5rem",

          margin: "auto",
        }}
      >
        <Avatar
          className={styles.img}
          style={{ width: "50px", height: "50px" }}
        >
          <img
            src={
              data?.brand?.brandImageUrl ? data?.brand?.brandImageUrl : bibaImg
            }
            alt="brand-logo"
            style={{ objectFit: "contain", width: "100%" }}
          />
        </Avatar>
      </Avatar>
      <Box className={styles.campaign}>
        <Box
          className={styles.text}
          style={{ margin: "auto", marginTop: "-5rem", paddingBottom: "2rem" }}
        >
          <div style={{ display: "flex" }}>
            <Typography variant="body1" className={styles.title}>
              {data?.Name}
            </Typography>
            {/* <img
              src={shareImg}
              style={{
                width: "0.8rem",
                height: "0.8rem",
                marginLeft: "0.8rem",
                marginTop: "0.3rem",
              }}
            /> */}
          </div>
          {data?.Payouts?.map((pay) => {
            if (pay?.type === "BARTER") {
              return (
                <a
                  href={pay?.productUrl}
                  className={styles.link}
                  style={{ margin: "auto" }}
                >
                  {pay?.type}
                  <img
                    src={webImg}
                    style={{ marginLeft: "0.2rem", height: "10px" }}
                  />
                </a>
              );
            }
          })}
        </Box>
      </Box>
      <Box className={styles.header}>
        <Box className={styles.data}>
          <Box className={styles.head}>
            <img
              src={data?.Platform === "YOUTUBE" ? Platform : instagram}
              alt="platform icon"
              style={{ width: "1rem", height: "0.7rem" }}
            />
            <Typography variant="body1" className={styles.key}>
              {/* {__tr("platform")} */}
              Platform
            </Typography>
          </Box>
          <Typography variant="body1" className={styles.value}>
            {data?.Platform}
          </Typography>
        </Box>

        {data?.Payouts?.map((pay) => {
          return (
            <>
              {/* <Box className={styles.data}>
                <Box className={styles.head}>
                  <IconCash size={22} />
                  <Typography variant="body1" className={styles.key}>
                    {/* {__tr("cash")} 
                    Cash payout
                  </Typography>
                </Box>
                <Typography variant="body1" className={styles.value}>
                  <img
                    src={rupeeImg}
                    alt="Rs"
                    style={{
                      objectFit: "contain",
                      width: "10px",
                      height: "12px",
                      imageResolution: "inherit",
                    }}
                  />
                  {pay?.cash?.amount}
                </Typography>
              </Box> */}
              <Box className={styles.data}>
                <Box className={styles.head}>
                  <img src={barter} alt="barter icon" />
                  <Typography variant="body1" className={styles.key}>
                    {/* {__tr("cash")} */}
                    {pay.type}
                  </Typography>
                </Box>
                <Typography variant="body1" className={styles.value}>
                  <img
                    src={rupeeImg}
                    alt="Rs"
                    style={{
                      objectFit: "contain",
                      width: "10px",
                      height: "12px",
                      imageResolution: "inherit",
                    }}
                  />
                  {getPayoutValue(pay)}
                </Typography>
              </Box>
            </>
          );
        })}
        <Box className={styles.data}>
          <Box className={styles.head}>
            <img src={paypersale} alt="paypersale icon" />
            <Typography variant="body1" className={styles.key}>
              Earn per scale
            </Typography>
          </Box>
          <Typography variant="body1" className={styles.value}>
            {data.CPACommissionRate}%
          </Typography>
        </Box>
        {/* <Box className={styles.badge}>
          <Typography variant="body2">Active</Typography>
        </Box> */}
      </Box>
    </>
  );
}

