import { Box } from '@material-ui/core';
import { ChangeEvent, useCallback, useEffect, useMemo, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Loader } from '../ui/Loader';
import { OTPInput } from '../ui/OTPInput';
import { SingleLineInput } from '../ui/SingleLineInput';
import { userFromUname } from '../../core/utils';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/ConfirmOtp.module.scss';
import axios from 'axios';
import { EmailOrPhone } from '../ui/EmailOrPhone';
import React from 'react';
import ReactDOM from 'react-dom';
import PasswordStrengthBar from 'react-password-strength-bar';
import Countdown from 'react-countdown';

export function ResetPassword(props: { user: any, emailorPhone: any }) {

  const [newPassword, setNewPassword] = useState('');
  const [cnfPassword, setCnfPassword] = useState('');

  const [password,setPassword] = useState('');
  const [otp, setOTP] = useState('');
  const [userKey, setUserKey] = useState('');
  const [errors, setErrors] = useState<any>({});
  const [loading, setLoading] = useState(false);
  const [emailOrPhone, setEmailOrPhone] = useState('');
  const [hasPendingOTP, setHasPendingOTP] = useState(false);
  const [userId, setUserId] = useState('');
  const [isOTPVerified, setIsOTPVerified] = useState(false);
  const [enableResendOtp, setResendOtp] = useState(false)
  setTimeout(() => {
    setResendOtp(true);
    // alert(enableResendOtp)
  }, 30000);
  const history = useHistory();


  const onResendOtp = useCallback(() => {
    setLoading(true);
    let isPhone = !props.emailorPhone.includes('@')
    if (isPhone) {
      let mob = props.emailorPhone.split(" ");
      axios.post('/api/auth/otp/send', { isPhone: true, countryCode: mob[0], number: mob[1] }).then(res => {
        setLoading(false);
        toast.success(__tr('msgOtpCodeSent'));
      });
    } else {
      axios.post('/api/auth/otp/send', { isPhone: false, email: props.user.email, name: props.user.name }).then(res => {
        setLoading(false);
        toast.success(__tr('msgOtpCodeSent'));
      });
    }

  }, [loading]);


  const check = useCallback((otp: string) => {
    setLoading(true);
    let isPhone = !props.emailorPhone.includes('@')
    if (isPhone) {
      let mob = props.emailorPhone.split(" ");
      axios.post('/api/auth/otp/verify', { isPhone: true, countryCode: mob[0], number: mob[1], otp, userId: props.user._id })
        .then(res => {

		  setUserKey(res.data.userKey);
          if (loading) {
            return;
          }


          setIsOTPVerified(true)
          setLoading(false)
          setOTP(true);

        }).catch(err => {
          setLoading(false)
          toast.error("Incorrect Otp")
        })
    } else {
      axios.post('/api/auth/otp/verify', { isPhone: false, otp, userId: props.user._id }).then(res => {
		setUserKey(res.data.userKey);
        setLoading(false);
        /// account verification is now complete.
        toast.success('Otp verified');
        setOTP(true);
        setIsOTPVerified(true)

        //history.replace(Routes.Home);

      }).catch(err => {
        toast.error("Otp not Matched")
        console.log('eeeeeeeeeee', err)
        setLoading(false)
      });
    }


  })
  const signIn = (password: string) => {
    const postBody = {
      password,
      email: props.user.email,
      phone: props.user.phone
    }
    axios.post('/api/auth/login', postBody).then(res => {
      localStorage.setItem('token', res.data.token);
      return history.replace(Routes.Home);
    }).catch((err) => {
      toast.error(err.message);
    });
  }

  const formValidation = (ev, inputType) => {
    ev.preventDefault();
    let formValue = ev.target.value;
    console.log('form', ev, ev.target.value);
    let newErrors: any = {};
    switch (inputType) {
      case 'NewPassword':
        if (!Validators.isValidPassword(formValue)) {
          newErrors.newPassword = __tr('errorInvalidPassword');
        }
        break;
      case 'CnfPassword':
        if (formValue !== newPassword) {
          newErrors.confirmPassword = __tr('errorPasswordDontMatch');
        }
        break;
      default:
        break;
    }
    setErrors(newErrors);
  }

  const onCompleted = useCallback(
    (ev: ChangeEvent<HTMLFormElement>) => {
      ev.preventDefault();
      if (loading) {
        return;
      }

      if (!otp) {
        toast.warn(__tr('pleaseEnterOtp'));
        return;
      }

      if (otp) {
        let form = ev.currentTarget;

        let newErrors: any = {};
        let password = form.newPassword.value;
        let confirm = form.confirmPassword.value;

        let passwordStatus=Validators.isValidPasswordNew(password)
        if (!passwordStatus.valid) {
          newErrors.newPassword = __tr(passwordStatus.message);
        }
        else {
          if (confirm !== password) {
            newErrors.confirmPassword = __tr('errorPasswordDontMatch');
          }
        }

        setErrors(newErrors);

        if (Object.keys(newErrors).length > 0) {
          return;
        }

        setLoading(true);

        if (props.user.phone) {
          const mob = props.user.phone.split(" ");
          if (mob.length === 2) {
            axios.post('/api/auth/password/reset', { userKey,isPhone: true, countryCode: mob[0], number: mob[1], otp, userId: props.user._id, password })
              .then(res => {
                console.log(res);
                setLoading(false);
                /// account verification is now complete.
                toast.success("Password Reset Successfully");
                // history.push(Routes.BaseCampaigns)
                signIn(password);
                //history.replace(Routes.Home);

              }).catch(err => {
                console.log('eeeeeeeeeee', err)
              })

              ;
          }
          else {
            toast.error("Incorrect Mobile Number");
          }
        } else {
          axios.post('/api/auth/password/reset', { userKey,isPhone: false, otp, userId: props.user._id, password }).then(res => {

            setLoading(false);
            /// account verification is now complete.
            toast.success("Password Reset Successfully");
            signIn(password);
            //history.replace(Routes.Home);

          }).catch(err => {
            console.log('eeeeeeeeeee', err)
          });
        }

        // user?.confirmPassword(otp, password, {
        //   onSuccess() {
        //     setLoading(false);
        //     toast.success(__tr('passwordReset'));
        //     history.replace(Routes.Login);
        //   },
        //   onFailure(err) {
        //     toast.error(__tr('failurePasswordReset'));
        //   },
        // });
      }
    },
    [loading, history, props.user, otp]
  );

  return (
    <div className={styles.loginWrapper}>
      <div className={styles.formWrapper}>
        <h3>{__tr('btnResetPassword')}</h3>
        <p className={styles.subtitle}>{__tr('pleaseEnterOtp')}</p>
        <form action='' className={styles.form} onSubmit={onCompleted}>
          {loading && <Loader />}
          {!isOTPVerified &&<Box marginY={2} marginTop={6}>
            <OTPInput length={6} onChange={check} />
          </Box>}
          {!isOTPVerified && <Box marginY={2} display='flex' flexDirection='column' alignItems='center' justifyContent='center'>
            {!enableResendOtp && <Countdown daysInHours={true} date={Date.now() + 30000} />},

            {enableResendOtp && <button className={styles.resendBtn} onClick={onResendOtp}>
              {__tr('resendOtp')}
            </button>}

          </Box>}

          {isOTPVerified && (
            <>
              <Box marginY={2}>
                <SingleLineInput
                  label={__tr('labelCreatePassword')}
                  name='newPassword'
                  placeholder='john@example.com'
                  type='password'
                  inputProps={{
                    onChange: (ev) => setNewPassword(ev.currentTarget.value),
                    // onBlur: (ev) => formValidation(ev, 'NewPassword'),
                    value: newPassword,
                  }}
                  error={errors.newPassword}
                />
              </Box>
              {console.log('newPassword', newPassword, errors?.newPassword)}
              {newPassword && <PasswordStrengthBar password={newPassword} />}
              <Box marginY={2}>
                <SingleLineInput
                  label={__tr('labelConfirmPassword')}
                  name='confirmPassword'
                  placeholder={__tr('placeholderSignupPassword')}
                  type='password'
                  inputProps={{
                    onChange: (ev) => setCnfPassword(ev.currentTarget.value),
                    // onBlur: (ev) => formValidation(ev, 'CnfPassword'),
                    value: cnfPassword,
                  }}
                  error={errors.confirmPassword}
                />
              </Box>
              <Box marginY={4}>
                <button type='submit' className={styles.btnSubmit}>
                  {__tr('btnSubmit')}
                </button>
              </Box>
            </>
          )}
        </form>
      </div>
    </div>
  );
}

