import { useEffect, useState } from 'react';
import { css } from '@emotion/css';
import { Box, Button, Divider, Typography, withStyles, RadioGroup, FormControlLabel, Radio, FormControl } from '@material-ui/core';
import { IconCart } from '../ui/Icons';
import { toast } from "react-toastify";
import { TextTransformNoneButton } from '../ui/TextTransformNoneButton';
import { callRazorPay, generatePaymentLink } from '../../core/utils';
import { CssVariables } from '../../css-variables';
import { __tr } from '../../i18n';
import { Application } from '../../models/index';
import { useSelectedCampaign, useSelectedCampaignId } from '../../state/selectors';
import { useDispatch } from 'react-redux';
import { setApplicants, setApplicantsApprove } from '../../state/action-creators';
import { Avatar } from '@material-ui/core';
import axios from 'axios';

const PayButton = withStyles({
  root: {
    padding: 16,
  },
})(TextTransformNoneButton);

const styles = {
  text: css`
    font-size: 14px;
    color: gray;
  `,
  orderTable: css`
    width: 100%;
    color: ${CssVariables.colorGrayV3};
  `,
  orderKey: css`
    font-weight: 500;
    padding: 8px 16px;
  `,
  orderValue: css`
    font-weight: 500;
    text-align: right;
  `,
  payOffline: css`
    color: gray;
    text-align: center;
  `,
};

interface Props {
  aplicantsList: Application[];
}
export function ApproveMilestones(props: Props) {

  const selectcampaign = useSelectedCampaign();

  const [checked, setchecked] = useState(false);

  const campaignId = useSelectedCampaignId();
  const dispatch = useDispatch();

  const aprrovelist = props.aplicantsList;

  console.log('aprrovelistaprrovelist', aprrovelist)
  const idex = aprrovelist.length !== 0 ? aprrovelist[0].milestones?.findIndex(m => (m?.status === 'Activated' || m?.status === 'Submitted' || m?.status === 'Revision' || !m?.status)) ?? -1 : -1;

  // @ts-ignore: Object is possibly 'null'.
  const approveMile = selectcampaign?.Milestones[idex];
  console.log('approveMile', approveMile)
  // @ts-ignore: Object is possibly 'null'.
  const pays = selectcampaign?.Payouts?.filter(a => a.type === 'VARIABLE' || a.type === 'FIXED') ?? [];

  const sum = pays.reduce((a: number, b: any) => { return (a + b.cash?.amount) }, 0);


  const approveAll = (idx: number, status: string) => {

    aprrovelist.forEach(applicant => {
      const miles = applicant.milestones;
      // @ts-ignore: Object is possibly 'null'.
      miles[applicant.selectedIdx].status = status;
      const token = localStorage.getItem('token');

      axios.put('/api/applications/' + applicant._id, { _id: applicant._id, milestones: miles, campaignId: campaignId }, { headers: { 'Authorization': token } }).then(updateres => {
        axios.get('/api/applications/campaign/' + campaignId, { headers: { 'Authorization': token } }).then(res => {
          dispatch(setApplicants(res.data));
          dispatch(setApplicantsApprove([applicant], "remove"));
          if (status === 'Revision') {
            toast.success("Revision Request Sent Successfully");
          } else {
            toast.success("Approved Successfully");
          }
        });
      })

    })


  }


  return (
    <Box>
      <Box
        marginBottom={1}
        padding={2}
        display='flex'
        flexDirection='row'
        alignItems='center'
        justifyContent='space-between'
      >
        <Box display='flex' flexDirection='row' alignItems='center' justifyContent='flex-start'>
          <Box marginRight={1}>
            <Avatar />
          </Box>
          <Typography variant='h6' style={{ textTransform: 'uppercase' }}>
            Milestone {idex + 1}
          </Typography>
        </Box>
      </Box>
      <Divider />
      <Box padding={2}>
        <table className={styles.orderTable}>
          <tbody>
            <tr>
              <td className={styles.orderKey} colSpan={2}>
                {approveMile?.milestoneTitle}
              </td>
            </tr>
            <tr>
              <td className={styles.orderKey}>{'Due Date'}</td>
              <td className={styles.orderValue}>{approveMile?.dueDate}</td>
            </tr>
            {approveMile?.percentage && approveMile?.percentage !== 0 && <tr>
              <td className={styles.orderKey}>{'Amount Percentage'}</td>
              {/*@ts-ignore: Object is possibly 'null'. */}
              <td className={styles.orderValue}>{approveMile?.percentage}%  ₹{approveMile?.percentage * sum / 100}</td>
            </tr>}
            {approveMile?.content?.map(m => {
              <tr>
                <td className={styles.orderKey} colSpan={2}>
                  <img style={{ marginLeft: '20px', width: '230px', height: '200px' }} src={m.uri}
                  />
                </td>
              </tr>
            })}

            <tr>
              <td className={styles.orderKey} colSpan={2}>
                Description
              </td>
            </tr>
            <tr>
              <td className={styles.orderKey} colSpan={2}>
                <textarea style={{ width: '250px' }} value={approveMile?.caption} />
              </td>
            </tr>
          </tbody>
        </table>
        <Box paddingY={2}>
          <PayButton
            fullWidth
            color='primary'
            variant='contained'
            disableElevation
            // @ts-ignore: Object is possibly 'null'.
            onClick={() => approveAll(idex, 'Approved')}
          >
            Approve
          </PayButton>
        </Box>
        <Box paddingTop={2} paddingY={2}>
          <PayButton
            fullWidth
            color='secondary'
            variant='contained'
            disableElevation
            onClick={() => approveAll(idex, 'Revision')}
          >
            Revision
          </PayButton>
        </Box>
      </Box>
    </Box>
  );
}
