/* eslint-disable react-hooks/rules-of-hooks */
import { useEffect, useState } from "react";
import axios from "axios";
import { Routes } from "../../../routes";
import { Link } from "react-router-dom";
import external from "../../../assets/images/external-link.svg";
import platfrom from "../../../assets/images/platform.svg";
import cash from "../../../assets/images/cash.svg";
import Avatar from "@material-ui/core/Avatar";

import { makeStyles, createStyles, Theme } from "@material-ui/core/styles";
import CampaignIcon from "../../../assets/images/Campaign Icon.svg";
import CampaignsDiscovery from "./CampaignsDiscovery";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    large: {
      width: theme.spacing(7),
      height: theme.spacing(7),
    },
  })
);
const FeatureCampaign = () => {
  const [newList, setNewList] = useState([]);
  const classes = useStyles();

  useEffect(() => {
    axios
      .get("https://wobb-ui.herokuapp.com/api/campaign/featured")
      .then((res: any) => {
        const data = res.data;
        const save = [];
        res.data.forEach((c) => {
          if (c.campaign && c.brand) {
            save.push(c);
          }
        });
        setNewList(save);
      });
  }, []);
  // console.log(newList);

  function handleType(type: any) {
    let data;
    switch (type.type) {
      case "VARIABLE":
        data = { amount: type?.maxCash?.amount };
        break;
      case "BARTER":
        data = { url: type?.productUrl, amount: type?.productMRP?.amount };
        break;
      case "FIXED":
        data = { amount: type?.cash?.amount };
        break;
      default:
        data = null;
    }
    return data;
  }

  return (
    <>
      <div className="center">
        <div className="container-box">
          <div className="row">
            <div className="col-lg-12">
              <div className="row feature_">
                <div className="col-lg-9 order-lg-1 order-md-1 px-0">
                  <div className="row">
                    <div className="col-lg-9 col-12 px-0">
                      <h1 className="f-heading">Featured Campaigns</h1>
                    </div>
                  </div>

                  {newList?.map((e: any, index) => {
                    const firstItem = (x: any) => (x.length > 0 ? x[0] : []);
                    let left = firstItem(
                      e?.campaign?.Payouts?.filter(
                        (x: any) => x.type == "VARIABLE"
                      ).map((x: any) => handleType(x))
                    );
                    let middle = firstItem(
                      e?.campaign?.Payouts?.filter(
                        (x: any) => x.type == "FIXED"
                      ).map((x: any) => handleType(x))
                    );

                    let right = firstItem(
                      e.campaign.Payouts.filter(
                        (x: any) => x.type == "BARTER"
                      ).map((x: any) => handleType(x))
                    );

                    return (
                      <Link to={Routes.viewCampaignDetails(e.campaign._id)}>
                        <div className="feature" key={index}>
                          <div className="row align-items-lg-center d-flex_">
                            <div
                              className="col-lg-1 col-3 f-icon b-img feature-p-0"
                              style={{
                                display: "flex",
                                alignItems: "center",
                                alignContent: "center",
                              }}
                            >
                              {e.brand.brandImageUrl ? (
                                <img
                                  src={e.brand.brandImageUrl}
                                  style={{ objectFit: "contain" }}
                                  alt={e.brand.name}
                                />
                              ) : (
                                <Avatar
                                  // style={{ objectFit: "contain" }}
                                  alt={e.brand.name}
                                  className={classes.large}
                                >
                                  {e.campaign.Name.charAt(0).toUpperCase()}
                                </Avatar>
                              )}
                            </div>
                            <div className="col-lg-5 col-9 no-padding vc feature-p-0">
                              <span className="feature-name">
                                {e.campaign.Name}
                              </span>

                              <a
                                rel="noreferrer"
                                target="_blank"
                                href={
                                  e.brand.website.startsWith("http")
                                    ? e.brand.website
                                    : `https://${e.brand.website}`
                                }
                                style={{
                                  margin: 0,
                                  color: "#848484",
                                  textDecoration: "none",
                                  marginTop: "2px",
                                }}
                              >
                                {e.brand.name}
                                <img
                                  src={external}
                                  height="18px"
                                  width="18px"
                                />
                              </a>
                            </div>
                            <div className="col-lg-2 col-4 feature-p-0">
                              <div className="row ">
                                <div className="col-lg-12">
                                  <span className="feature-item ">
                                    <img src={platfrom} alt="platform" />
                                    PLATFORM
                                  </span>
                                </div>
                                <div className="col-lg-12">
                                  <span className="feature-value">
                                    {e.campaign.Platform}
                                  </span>
                                </div>
                              </div>
                            </div>
                            {left?.amount ? (
                              <div className="col-lg-2 col-4 feature-p-0">
                                <div className="row">
                                  <div className="col-lg-12">
                                    <span className="feature-item">
                                      <img src={cash} alt="cash" />
                                      VARIABLE
                                    </span>
                                  </div>
                                  <div className="col-lg-12">
                                    <span className="feature-value">
                                      ₹ {` `} {left?.amount}
                                    </span>
                                  </div>
                                </div>
                              </div>
                            ) : null}
                            {middle?.amount ? (
                              <div className="col-lg-2 col-4 feature-p-0">
                                <div className="row">
                                  <div className="col-lg-12">
                                    <span className="feature-item">
                                      <img src={cash} alt="cash" />
                                      FIXED
                                    </span>
                                  </div>
                                  <div className="col-lg-12">
                                    <span className="feature-value">
                                      ₹ {` `} {middle?.amount}
                                    </span>
                                  </div>
                                </div>
                              </div>
                            ) : null}

                            <div className="col-lg-2 col-4 feature-p-0">
                              {right && (
                                <a
                                  rel="noreferrer"
                                  target="_blank"
                                  href={right.url}
                                  style={{
                                    margin: 0,
                                    color: "#848484",
                                    textDecoration: "none",
                                    marginTop: "2px",
                                  }}
                                  className="row"
                                >
                                  <div className="col-lg-12">
                                    <span className="feature-item">
                                      BARTER
                                      <img
                                        src={external}
                                        height="18px"
                                        width="18px"
                                      />
                                    </span>
                                  </div>
                                  <div className="col-lg-12">
                                    <span className="feature-value">
                                      {/* <a href={e.campaign.barterDetails?.productUrl}>Product Url</a> */}
                                      {/* (e.campaign.barter === true) &&  */}
                                      {
                                        <p
                                          style={{
                                            margin: 0,
                                            color: "#2e2e2e",
                                          }}
                                        >
                                          &#8377;
                                          {right?.amount ? right?.amount : 0}
                                        </p>
                                      }
                                    </span>
                                  </div>
                                </a>
                              )}
                            </div>
                          </div>
                        </div>
                      </Link>
                    );
                  })}
                </div>
                <div className="col-lg-3 order-lg-2 order-3 p-0 px-0">
                  <div className="back-img">
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="featured">
                          <div
                            className="row"
                            style={{ display: "flex", alignContent: "center" }}
                          >
                            <img src={CampaignIcon} alt="campaign" />
                            <h1>New campaign</h1>
                          </div>
                          <p className="camp-msg">
                            Post your free campaign and hire influencers in a
                            few minutes
                          </p>
                          <div className="post-camp-btn">
                            <a className="post-camp" href="/">
                              <Link to={Routes.Signup}>
                                {" "}
                                Post a free campaign
                              </Link>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-9 col-12 order-lg-3 order-2">
                  <div className="view-more-row">
                    <Link className="view-more" to={Routes.CampaignsDiscovery}>
                      View More
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default FeatureCampaign;
