  const [payout, setPayout] = useState<Payout[]>([]);
  const [fixedamount, setfixedamount] = useState(0);
  const [barteramount, setbarteramount] = useState(0);
  const [cpalink, setcpalink] = useState("");
  const [cparate, setcparate] = useState(20);
  const [cpaprice, setcpaprice] = useState(0);
