  const saveCampaignAsDraft = useCallback(async () => {
    let nextErrors = validateCampaign();
    if (Object.keys(nextErrors).length > 0) {
      // toast.warn(__tr("fixErrors"));

      Object.keys(nextErrors).forEach((er) => {
        // toast.warn(nextErrors[er]);
      });

      return;
    }

    let campaign = {
      ...computedCampaign,
      CampaignStatus: CampaignStatus.DRAFT,
    };

    try {
      setBusy(true);

      axios
        .post("/api/campaign", campaign, { headers: { Authorization: token } })
        .then((res) => {
          dispatch(appendCampaign(res.data));
          toast.success(__tr("savedAsDraft"));
          resetProperties();
          //onClose();
        });
      history.push(Routes.BaseCampaigns)
    } catch (error) {
      toast.warn((error as any).message);
    } finally {
      setBusy(false);
    }
  }, [computedCampaign, dispatch, resetProperties, validateCampaign, token]);
  const postCampaign = useCallback(async () => {
    setIsLoading(true);
    let nextErrors = validateCampaign();
    console.log('nextErrors', nextErrors)
    if (Object.keys(nextErrors).length > 0) {
      // toast.warn(__tr("fixErrors"));
      setIsLoading(false);
      Object.keys(nextErrors).forEach((er) => {
        // toast.warn(nextErrors[er]);
        console.log(nextErrors)
      });
      return;
    }

    let campaign = {
      ...computedCampaign,
      CampaignStatus: CampaignStatus.SUBMITTED,
    };

    try {
      setBusy(true);
      axios
        .post("/api/campaign", campaign, { headers: { Authorization: token } })
        .then((res) => {
          dispatch(appendCampaign(res.data));
          setIsLoading(false);
          toast.success(__tr("campaignPosted"));
          resetProperties();
          //onClose();
        });
      history.push(Routes.BaseCampaigns)
    } catch (error) {
      setIsLoading(false);
      toast.warn((error as any).message);
    } finally {
      setIsLoading(false);
      setBusy(false);
    }
  }, [computedCampaign, dispatch, resetProperties, validateCampaign, token]);

  const addQuestions = (value: string, i: number) => {
    const ques = customQuestions;
    const res: typeof ques = [];
    ques.forEach((q, k) => {
      if (i === k) {
        const n: QuizElement = {
          question: value,
          options: q.options,
        };
        res.push(n);
      } else {
        res.push(q);
      }
    });
    setCustomQuestions(res);
  };

  const changeOptions = (value: string, i: number, k: number) => {
    const ques = customQuestions;
    const res: typeof ques = [];
    ques.forEach((q, l) => {
      if (i === l) {
        //@ts-ignore: Object is possibly 'null'.
        const op: typeof q.options = [];
        q.options?.forEach((o, j) => {
          if (j === k) {
            op.push(value);
          } else {
            op?.push(o);
          }
        });
        const n: QuizElement = {
          question: q.question,
          options: op,
        };
        res.push(n);
      } else {
        res.push(q);
      }
    });

    setCustomQuestions(res);
  };
  const addOptions = useCallback(
    (i: number) => {
      const temp: typeof customQuestions = [];
      customQuestions.forEach((c, k) => {
        if (k === i) {
          const nop: (string | null)[] = [...(c.options ?? []), ""];

          const n = {
            question: c.question,
            options: nop,
          };
          temp.push(n);
        } else {
          temp.push(c);
        }
      });
      setCustomQuestions(temp);
    },
    [customQuestions]
  );

  const removeOptions = useCallback(
    (i: number, j: number) => {
      const temp: typeof customQuestions = [];
      customQuestions.forEach((c, k) => {
        if (k === i) {
          const nop: (string | null)[] = c.options ?? [];
          nop.splice(j, 1);
          const n = {
            question: c.question,
            options: nop,
          };
          temp.push(n);
        } else {
          temp.push(c);
        }
      });
      setCustomQuestions(temp);
    },
    [customQuestions]
  );

