import {
  Box,
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
} from "@material-ui/core";
import { useCallback, useEffect, useState } from "react";
import { CampaignFilters } from "../../core";
import { __tr } from "../../i18n";
import { usePostedCampaigns } from "../../state/selectors";
import styles from "../../styles/CampaignStatusList.module.scss";
import cn from "classnames";
import { UppercaseSbText } from "../ui/custom";

interface Props {
  filter: string;
  onChange?(filter: string): any;
  setstatusFlag: any;
}

export function CampaignStatusList(props: Props) {
  const [count, setCount] = useState<Number>();
  const [active, setActive] = useState("all");

  const onChangeFilter = useCallback(
    (filter: string) => {
      if (props.onChange && filter !== props.filter) {
        props.onChange(filter);
        window.analytics.track("Filter Campaign Status");
      }
    },
    [props]
    
  );

  const postedCampaigns = usePostedCampaigns();

  useEffect(() => {
    // console.log("postedCampaigns", postedCampaigns);
    setCount(postedCampaigns.length);
  });
  const changehandler = (x: any) => {
    window.analytics.track("Filter Campaign Status");
    if (x === "all") {
      props.setstatusFlag("");
    } else if (x === "active") {
      props.setstatusFlag("approved");
    } else if (x === "underReview") {
      props.setstatusFlag("submitted");
    } else {
      props.setstatusFlag(x);
    }
    
  };
  // console.log(CampaignFilters);

  // console.log(count);
  CampaignFilters.filter((x) => x.type === "all")[0].count =
    postedCampaigns.length;
  CampaignFilters.filter((x) => x.type === "underReview")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "SUBMITTED").length;
  CampaignFilters.filter((x) => x.type === "draft")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "DRAFT").length;
  CampaignFilters.filter((x) => x.type === "paused")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "PAUSED").length;
  // CampaignFilters.filter((x) => x.type === "expired")[0].count =
  //   postedCampaigns.filter((x) => x.CampaignStatus === "EXPIRED").length;
  CampaignFilters.filter((x) => x.type === "closed")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "CLOSED").length;
  CampaignFilters.filter((x) => x.type === "private")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "PRIVATE").length;
  CampaignFilters.filter((x) => x.type === "active")[0].count =
    postedCampaigns.filter((x) => x.CampaignStatus === "APPROVED").length;

  return (
    <Box className={styles.region}>
      <Box padding={2} className={styles.header}>
        <UppercaseSbText
          style={{ fontSize: "1 rem", fontWeight: "600", paddingTop: "15px" }}
        >
          {"Campaign Status"}
        </UppercaseSbText>
      </Box>
      <List style={{ width: "100%" }}>
        {CampaignFilters.map((filter, i) => {
          // setActive(props.filter.type);

          return (
            <ListItem
              button
              key={filter.type}
              onClick={() => {
                setActive(filter.type);
                onChangeFilter(filter.type);
                changehandler(filter.type);
              }}
              divider={i < CampaignFilters.length - 1}
              className={cn({
                [styles.filterItem]: true,
                [styles.active]: active === filter.type,
              })}
            >
              <ListItemText
                primary={__tr(filter.type)}
                primaryTypographyProps={{
                  className: cn({
                    [styles.itemText]: true,
                    [styles.active]: active === filter.type,
                  }),
                }}
              />
              <ListItemSecondaryAction
                className={cn({
                  [styles.count]: true,
                  [styles.active]: active === filter.type,
                })}
              >
                {filter.count}
              </ListItemSecondaryAction>
            </ListItem>
          );
        })}
      </List>
    </Box>
  );
}
