import * as React from "react";
import {
  Button,
  Dialog,
  DialogActions,
  TextareaAutosize,
} from "@material-ui/core";
import axios from "axios";
interface IProps {
  bid: any;
  cid: any;
  refresh(): any;
}

export default function Reject(props: IProps) {
  const [open, setOpen] = React.useState(false);
  const [status, setStatus] = React.useState();

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const token = localStorage.getItem("token");

  const updateCampaginStatus = () => {
    const updateCampaginStatus = {
      "status": "REJECTED",
      "campaignId": props.cid,
    };
    if (token) {
      axios
        .post(
          `/api/campaign/changeStatus/`,
          updateCampaginStatus,
          {
            headers: { Authorization: token },
          }
        )
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const reject = () => {
    // console.log("camp", props.cid, props.bid);
    updateCampaginStatus();
    handleClose();
    props.refresh()
  };

  return (
    <div>
      <Button variant="contained" onClick={handleClickOpen} color="secondary">
        Reject
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <div style={{ width: "60%", padding: "20px" }}>
          <TextareaAutosize
            aria-label="minimum height"
            onChange={(e: any) => setStatus(e.target.value)}
            minRows={3}
            placeholder="Reason for reject"
            style={{ width: 460 }}
          />
        </div>

        <DialogActions>
          <Button variant="contained" onClick={reject} color="secondary">
            Reject
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
