declare var Razorpay: any;

/// ONGOING



export function callRazorPay(cost: number, applicants: Application[],
  allMilestones: boolean, campaignId: string,
  dispatch: any, mobileNo: string, user: any) {

  let mobile = mobileNo;
  const mobS = mobileNo.split(" ");
  if (mobS.length > 1) {
    mobile = mobS[1];
  }

  const hireApplicants = () => {
    console.log('here', applicants);
    applicants.forEach(app => {
      const miles = app.milestones ?? [];
      if (miles?.length > 0) {
        if (allMilestones) {
          miles.forEach((a, i) => {
            // @ts-ignore: Object is possibly 'null'.
            miles[i].status = 'Activated';
          })
        } else {
          // @ts-ignore: Object is possibly 'null'.
          miles[app.selectedIdx??0].status = 'Activated';
        }
      }
      const token = localStorage.getItem('token');
      axios.put('/api/applications/' + app._id, { _id: app._id, applicationStatus: 'hired', milestones: miles }, { headers: { 'Authorization': token } }).then(updateres => {
        axios.get('/api/applications/campaign/' + campaignId, { headers: { 'Authorization': token } }).then(res => {
          dispatch(setApplicants(res.data));
          console.log('toActivatetoActivate',app);
          dispatch(setApplicantsApprove([app],"remove"));
        });
      })

    })
    toast.success("Activated Successfully");
  }
  const payout = cost * 100;
  const options = {
    key: RAZORPAY_KEY_ID, // Enter the Key ID generated from the Dashboard
    amount: String(payout),
    //"amount": "100",
    currency: 'INR',
    name: 'Wobb',
    description: 'Campaign Payment',
    image: '/static/media/logo.91598bd8.svg',
    prefill: {
      "name": user.name,
      "email": user.email,
      "contact": mobile
    },
    notes: {
      address: 'Razorpay Corporate Office',
    },
    theme: {
      color: '#3399cc',
    },
    handler: (response: any) => {
      hireApplicants()
      //alert(response.razorpay_payment_id);
    },
  };

  const rzp1 = new Razorpay(options);

  rzp1.on('payment.failed', (response: any) => {
    alert('Payment Failed. Please Try Again !');
    console.log(response);
  });
  if(Number(payout) > 0) {
    rzp1.open()
  } else {
    hireApplicants();
  }
  
}

