import * as React from "react";
import { ChangeEvent, useEffect, useState } from "react";
import { Button, Dialog, DialogActions, TextField } from "@material-ui/core";
import axios, { AxiosResponse } from "axios";
import { useAppUser } from '../../state/selectors';
import { nanoid } from "nanoid";

const defaultState = {
  title: "",
  about: "",
  image: "",
};
interface IProps {
  bid: any;
  cid: any;
  isDisabled?: boolean;
  title_prop: string;
  campaign?: any;
  refresh(): any;
}

type GroupSettings = {
  [setting: string]: boolean;
};

export default function Approve(props: IProps) {
  const { user } = useAppUser();
  const [open, setOpen] = useState(false);
  const [image, setimage] = useState<any>({ selectedFile: "" });
  const [bannerimage, setbannerimage] = useState<any>({ selectedFile: "" });
  const [imageUrl, setimageUrl] = useState<string>("");
  const [bannerUrl, setbannerUrl] = useState<string>("");
  const [title, settitle] = useState<string>(`${props.title_prop}`);
  const [about, setabout] = useState<string>();
  const [branddetails, setbranddetails] = useState<string>();

  const [groupSettings, setGroupSettings] = useState<GroupSettings>({
    onlyAdminCanSeeAll: false,
    onlyAdminUpdateGroupInfo: false,
    allParticipantsAdmin: false,
  });


  useEffect(() => {
    setabout(props?.campaign?.Moto);
    setbranddetails(props?.campaign?.brand?.details);
    setbannerUrl(props?.campaign?.bannerUrl??"");
    setimageUrl(props?.campaign?.brand?.brandImageUrl??"");
  },[props])
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handlerInputChange = (event: any) => {
    let files = event.target.files;
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (e: any) => {
      setimage({ selectedFile: e.target?.result });
    };
  };

  const handlerInputChangeBanner = (event: any) => {
    let files = event.target.files;
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (e: any) => {
      setbannerimage({ selectedFile: e.target?.result });
    };
  };
  const token = localStorage.getItem("token");
  const submit = () => {
    const formData = { file: image.selectedFile, name: "name" };
    const formData2 = { file: bannerimage.selectedFile, name: "name" };

    if (token) {
      if(image.selectedFile !== ""){
      axios
        .post(`/api/s3/upload`, formData, {
          headers: { Authorization: token },
        })
        .then((res) => {
          setimageUrl(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });}
        if(bannerimage.selectedFile !== ""){

              axios
        .post(`/api/s3/upload`, formData2, {
          headers: { Authorization: token },
        })
        .then((res) => {
          setbannerUrl(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
      }
    }
  };

  const createChatGroup = () => {
    const chatRoom =nanoid();

    const groupParticipants:any[] = [];
    groupParticipants.push({
        userId: user?._id,
        profilePic: user?.imgUrl,
        userName: user?.name,
    })

        const postB = {
          profilePic: bannerUrl,
          userName: title,
          groupSettings,
          message: '',
          time: "",
          numUnreadMessages: 0,
          isOnline: false,
          isGroupChat: true,
          userId: user?._id,
          groupParticipants,
          chatRoom
        }
        axios.post('http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms', postB).then(res=> {
         //   fetchchatrooms();
      });
      return chatRoom;
  }
  const updateCampaginName = (id: any) => {
    const chatroomId = createChatGroup();
    const updateCampaginName = { Name: title, bannerUrl: bannerUrl, Moto:about,chatroomId };
    if (token) {
      axios
        .put(`/api/campaign/${id}`, updateCampaginName, {
          headers: { Authorization: token },
        })
        .then((res) => {
          // console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const updateBrandlogo = (id: any) => {
    const updatelogo = { brandImageUrl: imageUrl,details:branddetails };
    if (token) {
      axios
        .put(`/api/brand/${id}`, updatelogo, {
          headers: { Authorization: token },
        })
        .then((res) => {
          // console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  // updatedData(61f8c695854008001907e255,);
  const updateCampaignName = (e: ChangeEvent<HTMLInputElement>) => {
    settitle(e.target.value);
  };

  const updateCampaignAbout = (e: ChangeEvent<HTMLInputElement>) => {
    setabout(e.target.value);
  };

  const updateCampaginStatusApproved = () => {
    const updateCampaginStatus = {
      status: "APPROVED",
      campaignId: props.cid,
    };
    if (token) {
      axios
        .post(`/api/campaign/changeStatus/`, updateCampaginStatus, {
          headers: { Authorization: token },
        })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const validate = () => {
    console.log('a',imageUrl);
    console.log('b',bannerUrl);
    if (imageUrl !== "" && bannerUrl !== "") {
      return true;
    } else if (imageUrl === "" && bannerUrl === "") {
      alert("please upload brand logo and banner image");
      return false;
    } else if (imageUrl === "" && bannerUrl !== "") {
      alert("please upload barnd image");
      return false;
    } else if (imageUrl !== "" && bannerUrl === "") {
      alert("please upload banner image");
      return false;
    }
  };
  const approve = () => {
    if (validate()) {
      updateCampaginName(props.cid);
      updateBrandlogo(props.bid);
      updateCampaginStatusApproved();
      props.refresh()
      handleClose();
    }
  };

  return (
    <div style={{ height: "50%", width: "50%" }}>
      <Button
        variant="contained"
        disabled={props.isDisabled}
        onClick={handleClickOpen}
        style={{
          backgroundColor: props.isDisabled ? "lightgrey" : "green",
          color: "white",
          marginRight: "20px",
        }}
      >
        Approve
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            padding: "20px",
          }}
        >
          <div className="upload-img">
            <div className="brand-img">
              <label for="file" style={{ color: "a7f5f5" }}>
                Brand Logo*
              </label>{" "}
              <br />
              <input
                type="file"
                name="select-image"
                id="select-image"
                accept="image/png, image/gif, image/jpeg"
                onChange={handlerInputChange}
                style={{ marginTop: "5px" }}
              />
                    </div>
                    {props?.campaign?.brand?.brandImageUrl && <img style={{width:'100px', height:'100px'}} src={props?.campaign?.brand?.brandImageUrl} />}
      
            <div className="banner-img" style={{ marginTop: "15px" }}>
              <label for="file" style={{ color: "a7f5f5" }}>
                Banner Image*
              </label>
              <br />
              <input
                type="file"
                name="select-image"
                id="select-image"
                accept="image/png, image/gif, image/jpeg"
                onChange={handlerInputChangeBanner}
              />
              <br />
              {props?.campaign?.bannerUrl && <img style={{width:'100px', height:'100px'}} src={props?.campaign?.bannerUrl} />}
              <br />
              <label>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={submit}
                  disabled={
                    image.selectedFile == "" && bannerimage.selectedFile == ""
                  }
                >
                  Upload Image
                </Button>
              </label>
            </div>
          </div>

          {imageUrl && bannerUrl? (
            <p style={{ color: "green" }}>Successful</p>
          ) : (
            ""
            // <p style={{ color: "red" }}>
            //   {" "}
            //   Please Upload Brand logo & Banner Images
            // </p>
          )}

          <TextField
            size="small"
            variant="outlined"
            fullWidth
            placeholder="Title"
            id="title"
            name="title"
            value={title}
            defaultValue={props.title_prop}
            onChange={updateCampaignName}
            style={{ marginTop: "0.75em" }}
          />
          {/* <label for="about" style={{ color: "a7f5f5" }}>
                Campaign Moto*
              </label> */}

          {/* <textarea
            size="small"
            variant="outlined"
            fullWidth
            placeholder="About"
            id="about"
            name="about"
            value={about}
            onChange={updateCampaignAbout}
            style={{
              marginTop: "0.75em",
              border: "2px solid #a5f5f5 ",
              borderRadius: "5px",
              padding: "10px",
              fontSize: "14px",
            }}
          /> */}
          <label for="about" style={{ color: "a7f5f5" }}>
                Brand Details*
              </label>

          <textarea
            size="small"
            variant="outlined"
            fullWidth
            placeholder="About"
            id="about"
            name="about"
            value={branddetails}
            onChange={(e)=>setbranddetails(e.target.value)}
            style={{
              marginTop: "0.75em",
              border: "2px solid #a5f5f5 ",
              borderRadius: "5px",
              padding: "10px",
              fontSize: "14px",
            }}
          />
          <DialogActions>
            <Button
              onClick={approve}
              style={{
                backgroundColor: "green",
                color: "white",
              }}
            >
              Approved
            </Button>
          </DialogActions>
        </div>
      </Dialog>
    </div>
  );
}
