import { useSelector } from "react-redux";
import { IAppState } from ".";

export function useAppUser() {
    return useSelector((state: IAppState) => state.userState);
}

export function useBrands() {
    return useSelector((state: IAppState) => state.globalState.brands);
}

export function useCampaigns() {
    return useSelector((state: IAppState) => state.globalState.campaigns);
}

export function useApplicants() {
    return useSelector((state: IAppState) => state.globalState.applicants);
}

export function useApplicantsApprove() {
    return useSelector((state: IAppState) => state.globalState.applicantsapprove);
}

export function useSelectedCampaignId() {
    return useSelector((state: IAppState) => state.globalState.selectedcampaignid);
}

export function useSelectedCampaign() {
    return useSelector((state: IAppState) => state.globalState.selectedcampaign);
}

export function usePostedCampaigns() {
    return useSelector((state: IAppState) => {
        // let user = state.userState.user;
        // return state.globalState.campaigns.filter((c) => c.uid === user.sub);
        return state.globalState.campaigns
    })
}
