import { Backdrop, Menu, MenuItem } from "@material-ui/core";
import React, { useEffect, useMemo, useState ,useCallback, useRef} from "react";
import { useWindowSize } from "../../../../core/hooks";
import { useAppUser } from "../../../../state/selectors";
import ChatControls from "../chatControls";
import io from "socket.io-client";
import ChatHeader from "../chatHeader";
import MessageReceived from "./messageReceived";
import styles from "./messagesArea.module.scss";
import MessageSent from "./messageSent";
import axios from "axios";

interface message {
  id: string;
  isSent: boolean;
  text: string;
  time: string;
  image?: string;
  username?: string;
  participantName?: string;
}

interface Props {
  fullName: string;
  profilePic: string;
  groupChat?: boolean;
  selectedchat?: any;
}

const MessagesArea: React.FC<Props> = ({
  fullName,
  profilePic,
  groupChat,
  selectedchat,
}) => {
  const [to, setto] = useState<String>("");
  const [messagesSample, setmessagesSample] = useState<Array<message>>([]);
  const [socket, setSocket] = useState<any>(null);
  const [messageSelected, setMessageSelected] = useState<message>();
  const [openOptionModal, setopenOptionModal] = useState<boolean>(false);
  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null);
  const [option, setOptionSelected] = useState<String>("");
  const messageRef = useRef<HTMLDivElement | null>();
  const { user } = useAppUser();

  useEffect(() => {
    fetchMsgs(selectedchat?.chatRoom, selectedchat?.userId);
  }, [selectedchat]);

  useEffect(() => {
    const socket = io(
      `http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com`
    );
    setSocket(socket);
    const username = user?._id;
    const room = selectedchat?.chatRoom;

    socket.emit('joinRoom', { username, room });
    socket.on('message', (message) => {
      console.log(' socket.on==>',message);
      if(message.username === user._id) {
        if(!selectedchat?.isGroupChat) {
          let image = null;
          let text = message.text;
          if(message.text.includes("#~#file#~#")) {
            const f = message.text.split("#~#");
            image=f[0];
            text = null;
          }
          const myMsgBody = {
            isSent: true,
            isSeen: true,
            text: text,
            time: message.time,
            image:image,
            to: selectedchat?.chatWith,
            userId: user._id,
            chatRoom: selectedchat?.chatRoom,
            isGroupChat:selectedchat?.isGroupChat
          }
          const toMsgBody = {
            isSent: false,
            isSeen: false,
            text: text,
            time: message.time,
            image:image,
            to: [user._id],
            userId: selectedchat?.chatWith[0],
            chatRoom: selectedchat?.chatRoom,
            isGroupChat:selectedchat?.isGroupChat
          }
          axios.post('http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat',myMsgBody).then(res=>{
            axios.post('http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat',toMsgBody).then(res2=> {
              fetchMsgs(selectedchat?.chatRoom,user._id);
            })
          });
          axios.put('http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms/update',myMsgBody);
          axios.put('http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms/update',toMsgBody);
        } else {
          let image = null;
          let text = message.text;
          let privateChatUser = [];
          if(message.text.includes("#~#file#~#")) {
            const f = message.text.split("#~#");
            text=null;
            image=f[0];
          } if(message.text.includes("#~~#")) {
            const f = message.text.split("#~~#");
            text=f[0];
            console.log('f',f);
            privateChatUser = JSON.parse(f[1]);

          }
          const myMsgBody = {
            isSent: true,
            isSeen: true,
            text: text,
            time: message.time,
            image:image,
            //to: selectedchat?.chatWith,
            userId: user._id,
            chatRoom: selectedchat?.chatRoom,
            isGroupChat:selectedchat?.isGroupChat
          }
          selectedchat?.groupParticipants.forEach((g)=>{

            let txtmsg = text;
            let txtmsg2 = text;
            if (g.userId !== user?._id) {
              const found = privateChatUser?.filter(p=>p.userId === g.userId);
              if(found.length > 0) {
                txtmsg= txtmsg.replace("@"+found[0].userName.substr(0, found[0].userName.length),'<b style="color:red;">'+"@"+found[0].userName.substr(0, found[0].userName.length)+'</b>');
              
              }
              const toMsgBody = {
                isSent: false,
                isSeen: false,
                text: txtmsg,
                time: message.time,
                image: null,
                //to: [user._id],
                userId: g.userId,
                participantName: user?.name,
                chatRoom: selectedchat?.chatRoom,
                isGroupChat: selectedchat?.isGroupChat,
              };
              if (privateChatUser?.length > 0 && found.length > 0) {
                axios.post(
                  "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat",
                  toMsgBody
                );
                toMsgBody.text=txtmsg2;
                axios.put(
                  "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms/update",
                  toMsgBody
                );
              } else if (privateChatUser?.length === 0) {
                axios.post(
                  "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat",
                  toMsgBody
                );
                axios.put(
                  "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms/update",
                  toMsgBody
                );
              }
            }
          });
          axios
            .post(
              "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat",
              myMsgBody
            )
            .then((res) => {
              fetchMsgs(selectedchat?.chatRoom, user._id);
            });
          axios.put(
            "http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chatrooms/update",
            myMsgBody
          );
        }
      } else {
        setTimeout(() => {
          fetchMsgs(selectedchat?.chatRoom, user._id);
        }, 2000);
      }

      //setMsg(message);
    });

    socket.on('getimage', (img) => {
      console.log(' socket.getimage ==>',img);
    });

    return () => socket.close();
  }, [selectedchat, user]);

  const fetchMsgs = (chatRoom: string, userId: string) => {
    axios
      .get(
        `http://ada8f51e6d5f44457b5382bfe6611da2-1328322169.ap-south-1.elb.amazonaws.com/api/chat/chatroom/user/${chatRoom}/${userId}`
      )
      .then((res) => {
        console.log("res");
        const data = res?.data?.reverse();
        setmessagesSample(data);
        scrollToBottom();
      });
  };

  const messageOption: Array<string> = useMemo(() => [
    "Reply",
    "Forward",
    "Copy",
    "Delete",
  ]);

  useEffect(() => {
    if (groupChat) {
      messageOption.push(`Message ${fullName}`);
    }
  }, [fullName, groupChat, messageOption]);

  // window screen width
  const [width] = useWindowSize();

  const handleOptionModalOpen = (el: HTMLElement | null, message: message) => {
    setAnchorEl(el);
    setMessageSelected(message);
    setopenOptionModal(true);
  };

  const handleOptionModalClose = () => {
    setopenOptionModal(false);
    setMessageSelected(undefined);
  };

  const onOptionSelect = (option: string) => {
    setOptionSelected(option);
  };

  // const scrollToBottom = () => {
  //   console.log('scrollToBottom', messagesEndRef, messagesEndRef.current);
  //   messagesEndRef.current.scrollIntoView({block: "end"})
  // //  const  elmnt = document.getElementById("content");
  // //  if(elmnt)
  // // messagesEndRef.scrollIntoView({block: "end", inline: "nearest"});
  // }
  // console.log('message selected', messageSelected);
  // console.log('option selected', option);
  const scrollToBottom = () => {
    if (messageRef.current) {
      messageRef.current.scrollIntoView(
        {
          behavior: 'smooth',
          block: 'end',
          inline: 'nearest'
        })
    }
  }

  return (
    <div className={styles.Container}>
      <ChatHeader fullName={fullName} profilePic={profilePic} />
      <div className={styles.MessageArea} >
        {messagesSample.map(({ id, isSent, text, time, image, participantName }) =>
          isSent ? (
            <MessageSent
              key={id}
              text={text}
              time={time}
              image={image ? image : ""}
            />
          ) : (
            <MessageReceived
              onClick={(e: React.MouseEvent<HTMLElement>) => {
                handleOptionModalOpen(e.currentTarget, {
                  id,
                  isSent,
                  text,
                  time,
                  image,
                });
              }}
              onClickDownArrow={(el: HTMLDivElement | null) => {
                handleOptionModalOpen(el, { id, isSent, text, time, image });
              }}
              key={id}
              text={text}
              time={time}
              userName={groupChat ? participantName : fullName}
              image={image ? image : ""}
              groupChat={!!groupChat}
              isSelected={!!messageSelected && messageSelected.id === id}
            />
          )
        )}
        <div ref={messageRef} />
        <Backdrop
          style={{
            color: "#333333",
            opacity: width >= 900 ? ".0" : ".55",
            zIndex: "200",
          }}
          open={openOptionModal}
          className={styles.backdrop}
          onClick={() => handleOptionModalClose()}
        >
          <>
            <Menu
              PaperProps={{
                style: {
                  width: 222,
                },
              }}
              anchorEl={anchorEl}
              open={openOptionModal}
              onClose={() => handleOptionModalClose()}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              getContentAnchorEl={null}
            >
              {messageOption.map((option) => {
                return (
                  <MenuItem
                    className={styles.list}
                    key={option}
                    onClick={() => onOptionSelect(option)}
                  >
                    <span>{option}</span>
                  </MenuItem>
                );
              })}
            </Menu>
          </>
        </Backdrop>
      </div>
      <ChatControls
        groupChat={!!groupChat}
        selectedchat={selectedchat}
        socket={socket}
      />
    </div>
  );
};

export default MessagesArea;
