import { useEffect, useState } from 'react';
import { css } from '@emotion/css';
import { Box, Button, Divider, Typography, withStyles,RadioGroup,FormControlLabel,Radio,FormControl } from '@material-ui/core';
import { IconCart } from '../ui/Icons';
import { TextTransformNoneButton } from '../ui/TextTransformNoneButton';
import { callRazorPay,generatePaymentLink } from '../../core/utils';
import { CssVariables } from '../../css-variables';
import { __tr } from '../../i18n';
import { Application } from '../../models/index';
import {useSelectedCampaign,useSelectedCampaignId } from '../../state/selectors';
import { useDispatch } from 'react-redux';
import { useAppUser } from '../../state/selectors';

const PayButton = withStyles({
  root: {
    padding: 16,
  },
})(TextTransformNoneButton);

const styles = {
  text: css`
    font-size: 14px;
    color: gray;
  `,
  orderTable: css`
    width: 100%;
    color: ${CssVariables.colorGrayV3};
  `,
  orderKey: css`
    font-weight: 500;
    padding: 8px 16px;
  `,
  orderValue: css`
    font-weight: 500;
    text-align: right;
  `,
  payOffline: css`
    color: gray;
    text-align: center;
  `,
};

interface Props {
  aplicantsList: Application[];
}
export function OrderInfluencerSummary(props: Props) {

  const selectcampaign = useSelectedCampaign();
  
  const [checked,setchecked]=useState(false);

  const campaignId = useSelectedCampaignId();
  const { user } = useAppUser();
const dispatch = useDispatch();


  const shortlisted = props.aplicantsList;
  //const sum = shortlisted.reduce((a,b)=>{return a+b.payouts},0);
  let sum = 0;
  
 
    shortlisted.forEach(s=>{
      const pays = s.payouts?.filter(a=> a.type === 'VARIABLE' || a.type === 'FIXED') ?? [];
      // @ts-ignore: Object is possibly 'null'.

      if(!checked && selectcampaign?.Milestones.length > 0) {
        // @ts-ignore: Object is possibly 'null'.
        const perc = Number(s.milestones[0]?.percentage);
        sum+=pays.reduce((a,b)=>{return (a+(b.cash??b.maxCash).amount)*(perc/100)},0);
      } else {
      sum+=pays.reduce((a,b)=>{return a+(b.cash??b.maxCash).amount},0);
    }
    })

  
  
    sum=Math.round((Number(sum) + Number.EPSILON) * 100) / 100
  let gst = sum *0.18;
  gst=Math.round((gst + Number.EPSILON) * 100) / 100
  return (
    <Box>
      <Box
        marginBottom={1}
        padding={2}
        display='flex'
        flexDirection='row'
        alignItems='center'
        justifyContent='space-between'
      >
        <Box display='flex' flexDirection='row' alignItems='center' justifyContent='flex-start'>
          <Box marginRight={1}>
            <IconCart size={24} />
          </Box>
          <Typography variant='h6' style={{ textTransform: 'uppercase' }}>
            {__tr('orderSummary')}
          </Typography>
        </Box>
      </Box>
      <Divider />
      <Box padding={2}>
      <FormControl component="fieldset">
      <RadioGroup row>
          
              <FormControlLabel
                control={<Radio size='small' color='primary' />}
                checked={!checked}
                onClick={()=>{setchecked(false)}}
                //defaultChecked={true}
                label={'Milestone 1'}
              />
              <FormControlLabel
                control={<Radio size='small' color='primary' />}
                checked={checked}
                onClick={()=>{setchecked(true)}}
                //defaultChecked={false}
                label={'All Milestones'}
              />
        </RadioGroup>
        </FormControl>
        <p className={styles.text}>{__tr('orderSummaryText')}</p>
        <table className={styles.orderTable}>
          <tbody>
            <tr>
              <td className={styles.orderKey}>{__tr('influencerShortlisted')}</td>
              <td className={styles.orderValue}>{shortlisted.length}</td>
            </tr>
            <tr>
              <td className={styles.orderKey}>{__tr('payout')}</td>
              <td className={styles.orderValue}>₹ {Number(sum)}</td>
            </tr>
            <tr>
              <td className={styles.orderKey}>{__tr('gst')}</td>
              <td className={styles.orderValue}>₹ {gst}</td>
            </tr>
            <tr>
              <td className={styles.orderKey}>{__tr('totalAmount')}</td>
              <td className={styles.orderValue}>₹ {Number(sum)+Number(gst)}</td>
            </tr>
          </tbody>
        </table>
        <Box paddingY={2}>
          <PayButton
            fullWidth
            color='primary'
            variant='contained'
            disableElevation
            // @ts-ignore: Object is possibly 'null'.
            onClick={() => {if (selectcampaign) {callRazorPay(Number(sum)+gst,shortlisted,checked,campaignId,dispatch,selectcampaign?.MobileNo,user )} else {
              alert('Please select a campaign');
            }}}
          >
            {__tr('payAndHire')} (₹ {Number(sum)+gst})
          </PayButton>
        </Box>
        <Box padding={1} paddingTop={2}>
          <p className={styles.payOffline}>
            {__tr('wantToPayOffline')}
            <Button variant='text' color='primary' onClick={()=> generatePaymentLink(sum+gst)}>
              {__tr('getPaymentLink')}
            </Button>
          </p>
        </Box>
      </Box>
    </Box>
  );
}
